import { useState } from "react";
import PropTypes from "prop-types";
import DefaultMapIcon from "../../utils/icons/DefaultMapIcon";
import CheckIcon from "../../utils/icons/CheckIcon";
import PlusIcon from "../../utils/icons/PlusIcon";
import XIcon from "../../utils/icons/XIcon";
import CircleIcon from "../../utils/icons/CircleIcon";
import QuestionPopOver from "../../utils/components/PopOver/QuestionPopOver";
import RemoveAllIcons from "../../utils/icons/RemoveAllIcons";
import RemoveAllCellsDialog from "../../utils/components/Dialogs/RemoveAllCellsDialog";

export default function PlotSelectContainer({
  title,
  selectedCells,
  isEditing,
  onRemovedCell,
  onAdd,
  onSave,
  circleIconColor,
}) {
  const [isHovered, setIsHovered] = useState(false);
  const [isDialogOpen, setIsDialogOpen] = useState(false);

  const handleAddClick = () => {
    onAdd();
  };

  const handleSaveClick = () => {
    onSave();
  };

  const handleRemoveCell = (cell) => {
    onRemovedCell(cell);
  };

  const handleRemoveAllCells = () => {
    setIsDialogOpen(true);
  };

  const handleConfirmRemoveAllCells = () => {
    selectedCells.forEach((cell) => onRemovedCell(cell));
    setIsDialogOpen(false);
  };

  const handleCloseDialog = () => {
    setIsDialogOpen(false);
  };

  const iconStrokeColor = isEditing ? "#3552EF" : "#000000";
  const hoverColor = "#3552EF";

  return (
    <div className="d-flex flex-column gap-4">
      <div className="d-flex align-items-center gap-1">
        <div
          className="d-flex justify-content-center"
          style={{ width: "0.7rem", height: "0.7rem", borderRadius: "50%" }}
        >
          <CircleIcon
            style={{ width: "0.5rem", height: "unset" }}
            color={circleIconColor}
          />
        </div>
        <span className="lh-1">{title}</span>
      </div>
      <div className="d-flex justify-content-between">
        <div className="d-flex align-items-center gap-2">
          <span>RTD</span>
          <QuestionPopOver
            style={{ width: "1rem", height: "1rem" }}
            text="Resistance Temperature Detector."
          />
        </div>
        <button
          className="border-0 bg-transparent fs-0-875 fw-600 d-flex align-items-center gap-2"
          onClick={isEditing ? handleSaveClick : handleAddClick}
          style={{ color: isHovered ? hoverColor : iconStrokeColor }}
          onMouseEnter={() => setIsHovered(true)}
          onMouseLeave={() => setIsHovered(false)}
        >
          {isEditing ? (
            <>
              <CheckIcon stroke={isHovered ? hoverColor : iconStrokeColor} />{" "}
              Save
            </>
          ) : (
            <>
              <PlusIcon stroke={isHovered ? hoverColor : iconStrokeColor} /> Add
            </>
          )}
        </button>
      </div>
      {isEditing && selectedCells.length === 0 && (
        <div className="add-rtd cursor-auto d-flex flex-column align-items-center justify-content-center gap-4">
          <DefaultMapIcon />
          <span className="fs-6 fw-400 closed-text-color">
            {" "}
            Select RTD on the map{" "}
          </span>
        </div>
      )}
      {selectedCells.length > 0 && (
        <div className="add-rtd-cell-container d-flex flex-row justify-content-between position-relative">
          <div className="add-rtd-cell cursor-auto">
            {selectedCells.map((cell) => (
              <div
                className="rtd-selected-cell d-flex align-items-center justify-content-between"
                key={cell}
              >
                <span className="fs-6 fw-500">{cell}</span>
                <span
                  className="remove-cell-button cursor-pointer d-flex align-items-center justify-content-center"
                  onClick={() => handleRemoveCell(cell)}
                  style={{ color: "#E3203F", cursor: "pointer" }}
                >
                  <XIcon hoverStroke="#E3203F" />
                </span>
              </div>
            ))}
          </div>
          <div
            className="remove-all-selected-cells"
            onClick={handleRemoveAllCells}
            data-testid="remove-all-cells"
          >
            <RemoveAllIcons />
          </div>
        </div>
      )}
      <RemoveAllCellsDialog
        open={isDialogOpen}
        onClose={handleCloseDialog}
        onConfirm={handleConfirmRemoveAllCells}
        data-testid="dialog"
      />
    </div>
  );
}

PlotSelectContainer.propTypes = {
  title: PropTypes.string,
  selectedCells: PropTypes.array,
  isEditing: PropTypes.bool,
  onRemovedCell: PropTypes.func,
  onAdd: PropTypes.func,
  onSave: PropTypes.func,
  circleIconColor: PropTypes.string,
};
