import chroma from "chroma-js";

export const formatHeatFlux = (value, scale = 10000) => {
  return (value / scale).toFixed(2);
};

// Function to format total power
export const formatPower = (power) => {
  if (power >= 1e6) {
    // Watts (W)
    return `${(power / 1e6).toFixed(1)} W`;
  } else if (power >= 1e3) {
    // Kilowatts (kW)
    return `${(power / 1e3).toFixed(2)} kW`;
  } else {
    // Watts (W)
    return `${power.toFixed(2)} W`;
  }
};

// Function to generate color scale
export const generateColorScale = (minVal, maxVal) => {
  return chroma
    .scale([
      "#0d0887",
      "#0000FF",
      "#f15060",
      "#b83d85",
      "#FFD700",
      "#FF8C00",
      "#eff822",
    ])
    .domain([minVal, maxVal]);
};

export const initializeColorScale = () =>
  chroma
    .scale([
      "#00008B",
      "#0000FF",
      "#00CED1",
      "#32CD32",
      "#FFD700",
      "#FF8C00",
      "#B22222",
    ])
    .domain([0, 1]);

