import { useState } from "react";
import PropTypes from "prop-types";
import DropDownCloseIcon from "../../../utils/icons/DropDownCloseIcon";
import DropDownOpenIcon from "../../../utils/icons/DropDownOpenIcon";
import UploadIcon from "../../../utils/icons/UploadIcon";
import UploadedCalibrationFileList from "./UploadedCalibrationFileList";
import CheckIcon from "../../../utils/icons/CheckIcon";
import axios from "axios";
import DEFAULT_OPTIONS from "../../../utils/websocketConfig";
import QuestionPopOver from "../../../utils/components/PopOver/QuestionPopOver";
import WarningAlert from "../../../utils/components/WarningAlert";

const CALIBRATION_FILE_URL = `http://${DEFAULT_OPTIONS.host}:8080/calibration_file`;

const validateCalibrationFile = (data) => {
  if (!Array.isArray(data)) {
    return "The file must be a JSON array.";
  }

  for (const item of data) {
    if (typeof item !== "object" || item === null) {
      return "Each entry in the array must be an object.";
    }

    if (typeof item.x !== "number" || typeof item.y !== "number") {
      return "Each object must have numeric 'x' and 'y' properties.";
    }

    if (!Array.isArray(item.coefs) || item.coefs.length !== 3) {
      return "Each object must have a 'coefs' array with exactly 3 numbers.";
    }

    for (const coef of item.coefs) {
      if (typeof coef !== "number") {
        return "All elements in 'coefs' must be numbers.";
      }
    }
  }

  return null;
};

export default function UploadTTVComponent({
  acceptedFormats,
  onFileUpload,
  onFileDelete,
}) {
  const [isActive, setIsActive] = useState(true);
  const [error, setError] = useState("");
  const [uploadedFileName, setUploadedFileName] = useState(null);
  const [fileUploaded, setFileUploaded] = useState(false);

  const toggleDropdown = () => {
    setIsActive(!isActive);
  };

  const handleFileChange = (event) => {
    setError("");
    const selectedFile = event.target.files[0];
    if (
      selectedFile &&
      selectedFile.type === "application/json" &&
      selectedFile.name.endsWith(".json")
    ) {
      const reader = new FileReader();
      reader.onload = (e) => {
        const content = e.target.result;
        if (typeof content === "string") {
          processFileContent(selectedFile.name, content);
        } else {
          handleError(
            "Error reading file contents. Make sure the file is in the right format."
          );
        }
      };
      reader.onerror = () =>
        handleError(
          "Error reading file contents. Make sure the file is in the right format."
        );
      reader.readAsText(selectedFile);
    } else {
      handleError(
        "Invalid file format. Please upload a .json file with the right structure."
      );
    }
  };

  const processFileContent = (fileName, content) => {
    try {
      const parsedData = JSON.parse(content);
      const validationError = validateCalibrationFile(parsedData);

      if (validationError) {
        handleError(validationError);
        return;
      }

      setUploadedFileName(fileName);
      setFileUploaded(true);
      if (onFileUpload) {
        onFileUpload(parsedData);
      }

      axios
        .post(CALIBRATION_FILE_URL, JSON.stringify(parsedData), {
          headers: {
            "Content-Type": "application/json",
          },
        })
        .then((response) => {
          setIsActive(false);
          if (response.status !== 200) {
            throw new Error(`HTTP error! status: ${response.status}`);
          }
        })
        .catch((error) => {
          handleError(`Failed to upload file: ${error.message}`);
        });
    } catch (error) {
      handleError("Invalid JSON content. Ensure the file is well-formed.");
    }
  };

  const handleError = (message) => {
    console.error(message);
    setError(message);
    setFileUploaded(false);
  };

  const handleFileDelete = () => {
    if (!uploadedFileName) {
      return;
    }
    setUploadedFileName(null);
    setFileUploaded(false);
    setError("");
    const fileInput = document.getElementById("file-upload");
    if (fileInput) {
      fileInput.value = null;
    }
    if (onFileDelete) {
      onFileDelete();
    }
  };

  return (
    <div className="select-menu d-flex flex-column" data-testid="file-upload">
      <div
        className="select d-flex align-items-center justify-content-between"
        onClick={toggleDropdown}
      >
        <div className="d-flex align-items-center">
          {isActive ? <DropDownOpenIcon /> : <DropDownCloseIcon />}
          <div className="d-flex align-items-center w-100 gap-2">
            <span
              className={`fs-6 fw-500 ${isActive ? "main-text-color" : "closed-text-color"}`}
            >
              {" "}
              TTV calibration file{" "}
            </span>
            <QuestionPopOver
              style={{ width: "1rem", height: "1rem" }}
              text="Mapping file specific to each TTV to display temperature data."
            />
          </div>
        </div>
        {!isActive && fileUploaded && (
          <div className="check-icon-circle d-flex align-items-start">
            <CheckIcon size="1rem" />
          </div>
        )}
      </div>
      <div className={`options-list ${isActive ? "active" : ""}`}>
        {!fileUploaded && (
          <div className="option d-flex flex-column gap-3">
            <label
              htmlFor="file-upload"
              className="upload-calibration-file upload-content d-flex flex-row align-items-center justify-content-between cursor-pointer mt-3"
            >
              <div className="d-flex flex-column">
                <span className="file-upload-label fs-6 fw-500 main-text-color">
                  {" "}
                  Upload calibration file{" "}
                </span>
                <span className="fs-0-875 fw-300 secondary-text-color">
                  {" "}
                  Only {acceptedFormats} allowed.{" "}
                </span>
                <input
                  type="file"
                  id="file-upload"
                  className="file-upload-input"
                  onChange={handleFileChange}
                  accept={acceptedFormats}
                />
              </div>
              <div className="upload-icon">
                <UploadIcon strokeColor="#8C41EC" />
              </div>
            </label>
            {error && (
              <div className="d-flex flex-column gap-3">
                <WarningAlert show={!!error} message={error} onClose={() => setError("")} />
                <div className="example-json-container d-flex flex-column gap-2 cursor-auto">
                  <span className="fs-0-875 fw-300 secondary-text-color">
                    Valid example
                  </span>
                  <pre
                    className="example-json"
                    style={{
                      whiteSpace: "pre-wrap",
                      fontFamily: "monospace",
                      backgroundColor: "#faf8fe",
                      padding: "10px",
                      borderRadius: "5px",
                    }}
                  >
                    {`[
  {
    "x": 0,
    "y": 0,
    "coefs": [1.0, 2.0, 3.0]
  },
  {
    "x": 1,
    "y": 1,
    "coefs": [4.0, 5.0, 6.0]
  }
]`}
                  </pre>
                </div>
              </div>
            )}
          </div>
        )}
        {isActive && (
          <UploadedCalibrationFileList
            uploadedFileName={uploadedFileName}
            onFileDelete={handleFileDelete}
          />
        )}
      </div>
    </div>
  );
}

UploadTTVComponent.propTypes = {
  acceptedFormats: PropTypes.string.isRequired,
  onFileUpload: PropTypes.func,
  onFileDelete: PropTypes.func,
};
