import { Checkbox } from "@mui/material";
import { styled } from "@mui/material/styles";
import CheckIcon from "../icons/CheckIcon";

// Custom styled checkbox icons
const BpIcon = styled("span")(({ theme }) => ({
  borderRadius: "0.125rem",
  width: "1.25rem",
  height: "1.25rem",
  backgroundColor: "#D0D5DD",
  ".Mui-focusVisible &": {
    outline: "2px auto rgba(19,124,189,.6)",
    outlineOffset: 2,
  },
  "input:hover ~ &": {
    backgroundColor: "#badbd0",
  },
  "input:disabled ~ &": {
    boxShadow: "none",
    background:
      theme.palette.mode === "dark"
        ? "rgba(57,75,89,.5)"
        : "rgba(206,217,224,.5)",
  },
}));

const BpCheckedIcon = styled(BpIcon)({
  backgroundColor: "#007D4E",
  backgroundImage:
    "linear-gradient(180deg,hsla(0,0%,100%,.1),hsla(0,0%,100%,0))",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  "&::before": {
    display: "block",
    width: "1rem",
    height: "1rem",
    content: "''",
  },
  "input:hover ~ &": {
    backgroundColor: "#066b44",
  },
});

// Custom Checkbox component
export default function BpCheckbox(props) {
  return (
    <Checkbox
      sx={{
        "&:hover": { bgcolor: "transparent" },
        padding: 0, // Add padding 0 to MuiCheckbox-root
      }}
      disableRipple
      color="default"
      checkedIcon={
        <BpCheckedIcon>
          <CheckIcon size="1.3rem" />
        </BpCheckedIcon>
      }
      icon={<BpIcon />}
      inputProps={{ "aria-label": "Checkbox demo" }}
      {...props}
    />
  );
}
