import { useEffect, useState } from "react";
import PropTypes from "prop-types";
import IconButton from "@mui/material/IconButton";
import { getFiles, FILE_KEY } from "../fileManager";
import CheckIcon from "../../../utils/icons/CheckIcon";
import FileOptionsPopover from "../../../utils/components/FileOptionsPopover";
import MoreVertIcon from "../../../utils/icons/MoreVertIcon";
import { ClippedText, CustomTooltip } from "../../../utils/dialogCommonProps";
import ConfirmRemoveFileDialog from "../../../utils/components/Dialogs/ConfirmRemoveFileDialog";

export default function UploadCalibrationFile({
  uploadedFileName,
  onFileDelete,
}) {
  const [anchorEl, setAnchorEl] = useState(null);
  const [selectedFile, setSelectedFile] = useState(null);
  const [files, setFiles] = useState({});
  const [confirmDialogOpen, setConfirmDialogOpen] = useState(false);

  useEffect(() => {
    const storedFiles = getFiles(FILE_KEY);
    setFiles(storedFiles);
  }, []);

  useEffect(() => {
    if (uploadedFileName) {
      const fileContent = getFiles()[uploadedFileName];
      const updatedFiles = { ...files, [uploadedFileName]: fileContent };
      setFiles(updatedFiles);
    }
  }, [uploadedFileName]);

  const openPopOver = (event, fileName) => {
    setAnchorEl(event.currentTarget);
    setSelectedFile(fileName);
  };

  const closePopOver = () => {
    setAnchorEl(null);
    setSelectedFile(null);
  };

  const downloadCalibrationFile = () => {
    const fileContent = files[selectedFile];
    const blob = new Blob([fileContent], { type: "application/ocret-stream" });
    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = selectedFile;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
    closePopOver();
  };

  const deleteCalibrationFile = () => {
    setConfirmDialogOpen(true);
  };

  const confirmCalibrationFileDeletion = () => {
    const updatedFiles = { ...files };
    delete updatedFiles[selectedFile];
    setFiles(updatedFiles);
    onFileDelete();
    closePopOver();
    setConfirmDialogOpen(false);
  };

  const cancelCalibrationFileDeletion = () => {
    setConfirmDialogOpen(false);
  };

  const getBaseFileName = (fileName) => {
    const lastUnderscoreIndex = fileName.lastIndexOf("_");
    if (lastUnderscoreIndex !== -1) {
      return fileName.substring(0, lastUnderscoreIndex);
    } else {
      return fileName;
    }
  };

  const uniqueFileNamesWithSuffix = Object.keys(files).filter((fileName) => {
    const baseName = getBaseFileName(fileName);
    return baseName !== fileName;
  });

  return (
    <>
      <ul
        className="p-0 m-0 d-flex flex-column powermap-list"
        data-testid="powermap-list"
      >
        {uniqueFileNamesWithSuffix.map((fileName) => (
          <li key={fileName} className="uploaded-ttv mt-3">
            <div className="d-flex flex-row align-items-center">
              <div className="d-flex gap-2">
                <div className="check-icon-square d-flex justify-content-center align-items-center">
                  <CheckIcon />
                </div>
                <span className="fs-0-875 fw-400">
                  <CustomTooltip title={fileName}>
                    <ClippedText width="11rem">{fileName}</ClippedText>
                  </CustomTooltip>
                </span>
              </div>
            </div>
            <div>
              <IconButton
                sx={{ padding: "0" }}
                disableRipple
                onClick={(event) => openPopOver(event, fileName)}
              >
                <MoreVertIcon />
              </IconButton>
              <FileOptionsPopover
                anchorEl={anchorEl}
                open={Boolean(anchorEl)}
                onClose={closePopOver}
                onDownload={downloadCalibrationFile}
                onDelete={deleteCalibrationFile}
              />
            </div>
          </li>
        ))}
      </ul>
      <ConfirmRemoveFileDialog
        open={confirmDialogOpen}
        onClose={cancelCalibrationFileDeletion}
        onConfirm={confirmCalibrationFileDeletion}
      />
    </>
  );
}

UploadCalibrationFile.propTypes = {
  uploadedFileName: PropTypes.string,
  uploadProgress: PropTypes.number,
  isUploading: PropTypes.bool,
  onFileDelete: PropTypes.func,
};
