import { setUserParamValidity } from "./components/ParameterField/setUserParamValidity";

export function getInitialParams(
  user,
  requiredParams = ["name", "email", "message", "title"]
) {
  const allParams = [
    {
      id: "title",
      name: "Descriptive title",
      type: "text",
      val: "",
      valid: undefined,
    },
    {
      id: "name",
      name: "Full name",
      type: "text",
      val: user?.name && user?.surname ? `${user?.name} ${user?.surname}` : "",
      valid: undefined,
    },
    {
      id: "email",
      name: "Email",
      type: "email",
      val: user?.email ? user?.email : "",
      valid: undefined,
    },
    {
      id: "message",
      name: "Write down your message",
      type: "message",
      val: "",
      valid: undefined,
    },
  ];

  const filteredParams = allParams.filter((param) =>
    requiredParams.includes(param.id)
  );

  return user?.name && user?.surname && user?.email
    ? filteredParams.filter((param) => param.id === "message")
    : filteredParams;
}

export function handleChangeParams(
  idx,
  val,
  formParams,
  setFormIsValid,
  setFormParams
) {
  let paramsNew = [...formParams];
  paramsNew[idx].val = val;

  const { formIsValid, formParams: updatedFormParams } =
    setUserParamValidity(paramsNew);
  setFormIsValid(formIsValid);
  setFormParams(updatedFormParams);
}

export function handleClose(
  setFormIsValid,
  setValid,
  setFormParams,
  { onClose, user }
) {
  setFormIsValid(false);
  setValid(false);
  setFormParams(getInitialParams(user));
  onClose();
}

export const getFileNameWithoutExtension = (fileName) => {
  return fileName.replace(/\.[^/.]+$/, "");
};
