import PropTypes from "prop-types";
import { Dialog, DialogContent } from "@mui/material";
import PowermapComponent from "./PowermapComponent";
import { useEffect, useState } from "react";
import DownloadIcon from "../../../utils/icons/DownloadIcon";
import TrashIcon from "../../../utils/icons/TrashIcon";
import GreyCloseIcon from "../../../utils/icons/GreyCloseIcon";
import { getFileNameWithoutExtension } from "../../../utils/paramUtils";
import loadingGif from "../../../assets/gifs/loading.gif";

export default function SelectPowermapDialog({
  open,
  onClose,
  file,
  data,
  onSelect,
  onDownload,
  onDelete,
}) {
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (data && data.length > 0) {
      setLoading(false);
    }
  }, [data]);

  const handleSelect = () => {
    onSelect();
  };

  const handleDownload = () => {
    if (file) {
      onDownload(file.id);
    }
  };

  const handleDelete = () => {
    if (file) {
      onDelete(file.id);
    }
  };

  return (
    <Dialog
      open={open}
      onClose={onClose}
      maxWidth="lg"
      fullWidth
      PaperProps={{
        style: {
          minHeight: "90vh",
          maxHeight: "90vh",
          display: "flex",
          flexDirection: "column",
          padding: "1.5rem",
          gap: "1.4rem",
        },
      }}
    >
      <div className="d-flex justify-content-between align-items-center">
        <span className="fs-1-125 fw-500">
          {getFileNameWithoutExtension(file.name)}
        </span>
        <div className="d-flex" style={{ gap: "0.75rem" }}>
          <div
            className="export d-flex align-items-center justify-content-center"
            style={{
              width: "2rem",
              height: "2rem",
              backgroundColor: "#F2F4F7",
              borderRadius: "0.125rem",
            }}
            onClick={handleDownload}
          >
            <DownloadIcon />
          </div>
          <div
            className="delete d-flex align-items-center justify-content-center"
            style={{
              width: "2rem",
              height: "2rem",
              backgroundColor: "#F2F4F7",
              borderRadius: "0.125rem",
            }}
            onClick={handleDelete}
          >
            <TrashIcon />
          </div>
          <div
            className="d-flex align-items-center justify-content-center"
            style={{
              width: "2rem",
              height: "2rem",
              backgroundColor: "#667085",
              borderRadius: "50%",
            }}
          >
            <GreyCloseIcon onClick={onClose} />
          </div>
        </div>
      </div>
      <DialogContent
        style={{
          display: "flex",
          flexDirection: "row",
          justifyContent: "center",
          width: "100%",
          height: "100%",
          padding: "1rem",
          position: "relative",
          backgroundColor: "#F2F4F7",
          borderRadius: "0.25rem",
        }}
      >
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            width: "100%",
            overflow: "auto",
          }}
        >
          {file && (
            <div className="d-flex flex-column justify-content-start h-100">
              {loading ? (
                <div className="d-flex h-100 align-items-center justify-content-center">
                  <img src={loadingGif} alt="Loading..." width="10%" />
                </div>
              ) : data && data.length > 0 ? (
                <div className="d-flex justify-content-between">
                  <PowermapComponent data={data} />
                </div>
              ) : (
                <p>No heatmap data available</p>
              )}
            </div>
          )}
        </div>
      </DialogContent>
      <div className="d-flex justify-content-center">
        <button className="btn-secondary-form w-10" onClick={handleSelect}>
          Select
        </button>
      </div>
    </Dialog>
  );
}

SelectPowermapDialog.propTypes = {
  open: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  file: PropTypes.shape({
    id: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    size: PropTypes.number,
    type: PropTypes.string,
    lastModified: PropTypes.number,
  }),
  data: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.number)),
  onSelect: PropTypes.func.isRequired,
  onDownload: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
};
