import { useState, useEffect, useRef } from "react";
import PropTypes from "prop-types";
import { v4 as uuidv4 } from "uuid";
import DropDownCloseIcon from "../../../utils/icons/DropDownCloseIcon";
import DropDownOpenIcon from "../../../utils/icons/DropDownOpenIcon";
import UploadIcon from "../../../utils/icons/UploadIcon";
import UploadedPowermapList from "./UploadedPowermapList";
import CheckIcon from "../../../utils/icons/CheckIcon";
import Papa from "papaparse";
import SelectPowermapDialog from "./SelectPowermapDialog";
import { useTTVWSContext } from "../../../api/webSocketService";
import QuestionPopOver from "../../../utils/components/PopOver/QuestionPopOver";
import WarningAlert from "../../../utils/components/WarningAlert";

export default function UploadPowermapComponent({
  onFileUpload,
  onPowermapSelectionChange,
  onFileDelete,
}) {
  const [isActive, setIsActive] = useState(true);
  const [error, setError] = useState("");
  const [uploadedFiles, setUploadedFiles] = useState([]);
  const [selectedFiles, setSelectedFiles] = useState([]);
  const [dialogOpen, setDialogOpen] = useState(false);
  const [selectedFile, setSelectedFile] = useState(null);
  const [heatmapData, setHeatmapData] = useState([]);
  const [totalPower, setTotalPower] = useState(0);

  const { sendPowermap } = useTTVWSContext();

  useEffect(() => {
    onPowermapSelectionChange(selectedFiles.length > 0);
  }, [selectedFiles, onPowermapSelectionChange]);

  const toggleDropdown = () => {
    setIsActive(!isActive);
  };

  const handleFileChange = (event) => {
    const selectedFile = event.target.files[0];
    if (selectedFile) {
      if (
        selectedFile.type === "text/csv" ||
        selectedFile.type === "application/vnd.ms-excel"
      ) {
        setError("");
        uploadPowermap(selectedFile);
      } else {
        setError("Only CSV files are allowed");
      }
    }
  };

  const uploadPowermap = (fileToUpload) => {
    if (fileToUpload) {
      const fileExists = uploadedFiles.some(
        (file) => file.name === fileToUpload.name
      );
      if (fileExists) {
        setError("This powermap has already been uploaded.");
        return;
      }
      const reader = new FileReader();
      reader.onload = (e) => {
        const fileContent = e.target.result;
        parseCsvFile(fileToUpload, fileContent);
      };
      reader.readAsText(fileToUpload);
    }
  };

  const parseCsvFile = (file, fileContent) => {
    Papa.parse(fileContent, {
      complete: (results) => {
        if (results.errors.length > 0) {
          setError("Error parsing CSV file");
          return;
        }

        // Excluir la primera fila y procesar los datos
        const dataWithoutHeader = results.data.slice(1);
        
        // Convertir a números y manejar valores vacíos
        const numericData = dataWithoutHeader.map(row =>
          row.map(cell => {
            const value = cell.trim();
            return value === '' ? 0 : Number(value);
          })
        );

        // Validar que todos los valores sean números
        if (numericData.some(row => row.some(cell => isNaN(cell)))) {
          setError("Invalid powermap data: Contains non-numeric values");
          return;
        }

        // Calcular el valor máximo
        const maxValue = Math.max(...numericData.flat());

        // Enviar los datos al servidor en el formato exacto requerido
        sendPowermap(file.name, fileContent);

        // Actualizar el estado local
        const fileToSave = {
          id: uuidv4(),
          name: file.name,
          size: file.size,
          type: file.type,
          lastModified: file.lastModified,
          content: fileContent,
          totalPower: maxValue
        };

        const newUploadedFiles = [...uploadedFiles, fileToSave];
        setUploadedFiles(newUploadedFiles);
        setHeatmapData(numericData);
        setTotalPower(maxValue);
        onFileUpload && onFileUpload(true);

        // Abrir el diálogo con el archivo subido
        setSelectedFile(fileToSave);
        setDialogOpen(true);
        setIsActive(false);
      },
      header: false,
      skipEmptyLines: true
    });
  };

  const downloadPowermap = (fileId) => {
    const file = uploadedFiles.find((file) => file.id === fileId);
    if (!file) {
      return;
    }
    const blob = new Blob([file.content], { type: file.type });
    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = file.name;
    document.body.appendChild(a);
    a.click();
    URL.revokeObjectURL(url);
    document.body.removeChild(a);
  };

  const fileInputRef = useRef(null);

  const deletePowermap = (fileId) => {
    const newUploadedFiles = uploadedFiles.filter((file) => file.id !== fileId);
    setUploadedFiles(newUploadedFiles);
    closerDialog();
    onFileUpload && onFileUpload(false);
    setSelectedFiles((prevSelectedFiles) =>
      prevSelectedFiles.filter((file) => file.id !== fileId)
    );
    if (onFileDelete) {
      onFileDelete();
    }
    if (fileInputRef.current) {
      fileInputRef.current.value = "";
    }
  };

  const openDialog = (file) => {
    setSelectedFile(file);
    setDialogOpen(true);
  };

  const closerDialog = () => {
    setDialogOpen(false);
    setSelectedFile(null);
  };

  const selectPowermap = () => {
    if (selectedFile) {
      const isSelected = selectedFiles.some((f) => f.id === selectedFile.id);
      const updatedSelectedFiles = isSelected
        ? selectedFiles.filter((f) => f.id !== selectedFile.id)
        : [...selectedFiles, selectedFile];
  
      // Procesar el archivo CSV seleccionado
      Papa.parse(selectedFile.content, {
        complete: (results) => {
          // Excluir la primera fila y procesar los datos
          const dataWithoutHeader = results.data.slice(1);
          
          // Convertir a números y manejar valores vacíos
          const numericData = dataWithoutHeader.map(row =>
            row.map(cell => {
              const value = cell.trim();
              return value === '' ? 0 : Number(value);
            })
          );
  
          // Calcular el valor máximo
          const maxValue = Math.max(...numericData.flat());
  
          // Crear el objeto powermap en el formato requerido
          const powermapData = {
            powermap: {
              max_val: maxValue,
              series: numericData
            }
          };
  
          // Enviar los datos al servidor
          sendPowermap(selectedFile.name, JSON.stringify(powermapData));
        },
        header: false,
        skipEmptyLines: true
      });
  
      setSelectedFiles(updatedSelectedFiles);
    }
    closerDialog();
  };

  const handleCheckboxSelect = (file, isSelected) => {
    const updatedSelectedFiles = isSelected
      ? [...selectedFiles, file]
      : selectedFiles.filter((selectedFile) => selectedFile.id !== file.id);
    setSelectedFiles(updatedSelectedFiles);
    if (onPowermapSelectionChange) {
      onPowermapSelectionChange(updatedSelectedFiles.length > 0);
    }
  };

  return (
    <div
      className="select-menu d-flex flex-column w-100"
      data-testid="file-upload"
    >
      <div
        className="select d-flex align-items-center justify-content-between"
        onClick={toggleDropdown}
      >
        <div className="d-flex align-items-center">
          {isActive ? <DropDownOpenIcon /> : <DropDownCloseIcon />}
          <div className="d-flex align-items-center gap-2">
            <span
              className={`fs-6 fw-500 ${isActive ? "main-text-color" : "closed-text-color"}`}
            >
              {" "}
              Power maps{" "}
            </span>
            <QuestionPopOver
              style={{ width: "1rem", height: "1rem" }}
              text="A power map is a matrix of values that correspond to the spatial power distribution across the surface of a chip."
              onClick={(event) => event.stopPropagation()}
            />
          </div>
        </div>
        {!isActive && selectedFiles.length > 0 && (
          <div className="check-icon-circle d-flex align-items-start">
            <CheckIcon size="1rem" />
          </div>
        )}
      </div>
      {isActive && (
        <div className="options-list active mt-3">
          <div className="option d-flex flex-column gap-3">
            <label
              htmlFor="file-upload-powermap"
              className="upload-powermap upload-content d-flex flex-row align-items-center justify-content-between cursor-pointer"
            >
              <div className="d-flex flex-column">
                <span className="file-upload-label fs-6 fw-500 main-text-color">
                  {" "}
                  Upload powermap{" "}
                </span>
                <span className="fs-0-875 fw-300 secondary-text-color">
                  {" "}
                  Only .csv allowed.{" "}
                </span>
                <input
                  type="file"
                  id="file-upload-powermap"
                  className="file-upload-input"
                  onChange={handleFileChange}
                  accept=".csv"
                />
              </div>
              <div className="upload-icon">
                <UploadIcon strokeColor="#3552EF" />
              </div>
            </label>
            {error && (
              <WarningAlert show={!!error} message={error} onClose={() => setError("")} />
            )}
          </div>
          <UploadedPowermapList
            files={uploadedFiles}
            selectedFiles={selectedFiles}
            totalPower={totalPower}
            onItemSelected={(data) => {
              setSelectedFiles(data);
              if (onPowermapSelectionChange) {
                onPowermapSelectionChange(data.length > 0);
              }
            }}
            onDownload={downloadPowermap}
            onDelete={deletePowermap}
            onDialogOpen={openDialog}
            onSelect={handleCheckboxSelect}
          />
        </div>
      )}
      {selectedFile && (
        <SelectPowermapDialog
          open={dialogOpen}
          onClose={closerDialog}
          file={selectedFile}
          data={heatmapData}
          onSelect={selectPowermap}
          onDownload={downloadPowermap}
          onDelete={deletePowermap}
        />
      )}
    </div>
  );
}

UploadPowermapComponent.propTypes = {
  onFileUpload: PropTypes.func,
  onPowermapSelectionChange: PropTypes.func.isRequired,
  onFileDelete: PropTypes.func,
};