import { useEffect, useMemo, useState, useRef } from "react";
import Plot from "react-plotly.js";
import PropTypes from "prop-types";

export default function RenderPlot({ plot, index }) {
  const chartRef = useRef(null);

  const allTemperatures =
    plot?.data?.datasets?.flatMap((dataset) => dataset.data) || [];
  const minTemperature = allTemperatures.length
    ? Math.min(...allTemperatures)
    : 0;
  const maxTemperature = allTemperatures.length
    ? Math.max(...allTemperatures)
    : 100;

  const [layout, setLayout] = useState({
    title: {
      font: {
        family: "Arial, sans-serif",
        size: 24,
        color: "#333",
      },
    },
    xaxis: {
      title: {
        text: "Time (seconds)",
        font: {
          family: "Arial, sans-serif",
          size: 18,
          color: "#333",
        },
        standoff: 20,
        autorange: false,
        range: [0, 100],
        xanchor: "left",
        yanchor: "top",
      },
      type: "linear",
      tickformat: "d",
    },
    yaxis: {
      autorange: true,
      range: [minTemperature, maxTemperature],
      tickvals: Array.from(
        { length: 6 },
        (_, i) => minTemperature + (i * (maxTemperature - minTemperature)) / 5
      ),
    },
    margin: {
      l: 50,
      r: 50,
      b: 50,
      t: 50,
      pad: 4,
    },
    paper_bgcolor: "#f8f9fa",
    plot_bgcolor: "#f8f9fa",
  });

  const handleRelayout = (eventData) => {
    if (eventData["xaxis.range"] || eventData["yaxis.range"]) {
      setLayout((prevLayout) => ({
        ...prevLayout,
        xaxis: {
          ...prevLayout.xaxis,
          range: eventData["xaxis.range"] || prevLayout.xaxis.range,
        },
        yaxis: {
          ...prevLayout.yaxis,
          range: eventData["yaxis.range"] || prevLayout.yaxis.range,
        },
      }));
    }
  };

  useEffect(() => {
    setLayout((prevLayout) => ({
      ...prevLayout,
      yaxis: {
        ...prevLayout.yaxis,
        range: [minTemperature, maxTemperature],
        tickvals: Array.from(
          { length: 6 },
          (_, i) => minTemperature + (i * (maxTemperature - minTemperature)) / 5
        ),
      },
    }));
  }, [plot?.data]);

  const plotlyData = useMemo(
    () =>
      plot?.data?.datasets?.map((dataset, index) => ({
        x: plot.data.labels,
        y: dataset.data,
        type: "scatter",
        mode: "lines",
        name: dataset.label,
        line: {
          color: `hsl(${(index * 360) / plot.data.datasets.length}, 100%, 50%)`,
          shape: "linear",
        },
        fill: dataset.fill ? "tozeroy" : "none",
      })) || [],
    [plot?.data]
  );

  const config = {
    displayModeBar: true,
    modeBarButtonsToRemove: ["sendDataToCloud"],
    displaylogo: false,
  };

  return (
    <div
      className="plot-container"
      key={`ploty_${plot.id}_${index}`}
      data-testid="plotly-component"
      style={{
        display: "flex",
        flexDirection: "column",
        height: "100%",
        padding: "1rem",
        backgroundColor: "#fff",
        borderRadius: "0.5rem",
        boxShadow: "0 0 10px rgba(0,0,0,0.05)"
      }}
    >
      <div className="plot-header" style={{ marginBottom: "0.5rem" }}>
        <span className="fs-6 fw-500">{plot?.title || "Untitled Plot"}</span>
        <span>/</span>
        <span
          className="fs-6 fw-500 grey-text-color"
          data-testid="max-min-temperature"
        >
          {" "}
          Temperature °C
        </span>
        <span className="fs-6 fw-500 grey-text-color">
          Max: {maxTemperature} °C
        </span>
      </div>
      <div className="plot-content" style={{ flex: 1, minHeight: 0 }}>
        <Plot
          data={plotlyData}
          layout={{
            ...layout,
            autosize: true,
            height: null  // Esto permite que el plot se ajuste al contenedor
          }}
          config={config}
          style={{ width: "100%", height: "100%" }}
          useResizeHandler={true}
          ref={chartRef}
          onRelayout={handleRelayout}
        />
      </div>
    </div>
  );
}

RenderPlot.propTypes = {
  plot: PropTypes.object.isRequired,
  index: PropTypes.number.isRequired,
};
