import { useState } from "react";
import PropTypes from "prop-types";
import { IconButton } from "@mui/material";
import MoreVertIcon from "../../../utils/icons/MoreVertIcon";
import FileOptionsPopover from "../../../utils/components/FileOptionsPopover";
import { CustomTooltip, ClippedText } from "../../../utils/dialogCommonProps";
import BpCheckbox from "../../../utils/components/BpCheckbox";
import { formatPower } from "../../../utils/heatFluxHelper";
import CircleIcon from "../../../utils/icons/CircleIcon";
import QuestionPopOver from "../../../utils/components/PopOver/QuestionPopOver";
import { getFileNameWithoutExtension } from "../../../utils/paramUtils";
import ConfirmRemoveFileDialog from "../../../utils/components/Dialogs/ConfirmRemoveFileDialog";

export default function UploadedPowermapList({
  files = [],
  selectedFiles = [],
  onItemSelected,
  onDownload,
  onDelete,
  onDialogOpen,
  totalPower,
  onSelect,
}) {
  const [popoverAnchorEl, setPopoverAnchorEl] = useState(null);
  const [selectedFile, setSelectedFile] = useState(null);
  const [confirmDialogOpen, setConfirmDialogOpen] = useState(false);

  const openPopOver = (event, file) => {
    setPopoverAnchorEl(event.currentTarget);
    setSelectedFile(file);
  };

  const closePopOver = () => {
    setPopoverAnchorEl(null);
    setSelectedFile(null);
  };

  const onCheckboxChange = (file) => {
    if (!Array.isArray(selectedFiles)) {
      console.error("selectedFiles is not an array:", selectedFiles);
      return;
    }
    const isSelected = selectedFiles.some(
      (selectedFile) => selectedFile.id === file.id
    );
    const updatedSelectedFiles = isSelected ? [] : [file];
    onItemSelected(updatedSelectedFiles);
    onSelect(file, !isSelected); // Call the onSelect function with the file and its new selection state
  };

  const downloadPowermap = () => {
    if (selectedFile) {
      onDownload(selectedFile.id);
    }
    closePopOver();
  };

  const deletePowermap = () => {
    setConfirmDialogOpen(true);
  };

  const confirmPowermapDeletion = () => {
    if (selectedFile) {
      onDelete(selectedFile.id);
    }
    setConfirmDialogOpen(false);
    closePopOver();
  };

  const cancelPowermapDeletion = () => {
    setConfirmDialogOpen(false);
    closePopOver();
  };

  return (
    <div>
      <ul className="p-0 m-0 mt-3 d-flex flex-column powermap-list">
        {Array.isArray(files) &&
          files.map((file, index) => (
            <li
              key={index}
              className={`uploaded-powermap ${
                Array.isArray(selectedFiles) &&
                selectedFiles.some(
                  (selectedFile) => selectedFile.id === file.id
                )
                  ? "selected"
                  : ""
              }`}
            >
              <div className="d-flex gap-2 w-100">
                <BpCheckbox
                  checked={
                    Array.isArray(selectedFiles) &&
                    selectedFiles.some(
                      (selectedFile) => selectedFile.id === file.id
                    )
                  }
                  onChange={() => onCheckboxChange(file)}
                />
                <span
                  className="fs-0-875 fw-400"
                  onClick={() => onDialogOpen(file)}
                >
                  <div className="d-flex align-items-center gap-2">
                    <CustomTooltip title={file.name}>
                      <ClippedText width="4rem">
                        {getFileNameWithoutExtension(file.name)}
                      </ClippedText>
                    </CustomTooltip>
                    <CircleIcon style={{ width: "0.25rem" }} />
                    <div className="d-flex align-items-center gap-2">
                      <span>
                        {formatPower(
                          totalPower !== undefined ? totalPower : "N/A"
                        )}
                      </span>
                      <QuestionPopOver
                        style={{ width: "1rem", height: "1rem" }}
                        text="It's a .csv file with set dimensions and varying numbers assigned to each cell."
                      />
                    </div>
                  </div>
                </span>
              </div>
              <div>
                <IconButton
                  sx={{ padding: "0" }}
                  disableRipple
                  onClick={(event) => openPopOver(event, file)}
                >
                  <MoreVertIcon />
                </IconButton>
                <FileOptionsPopover
                  anchorEl={popoverAnchorEl}
                  open={Boolean(popoverAnchorEl && selectedFile === file)}
                  onClose={closePopOver}
                  onDownload={downloadPowermap}
                  onDelete={deletePowermap}
                />
              </div>
            </li>
          ))}
      </ul>
      <ConfirmRemoveFileDialog
        open={confirmDialogOpen}
        onClose={cancelPowermapDeletion}
        onConfirm={confirmPowermapDeletion}
      />
    </div>
  );
}

UploadedPowermapList.propTypes = {
  files: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string.isRequired,
      name: PropTypes.string.isRequired,
      size: PropTypes.number,
      type: PropTypes.string,
      lastModified: PropTypes.number,
    })
  ).isRequired,
  selectedFiles: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string.isRequired,
      name: PropTypes.string.isRequired,
      size: PropTypes.number,
      type: PropTypes.string,
      lastModified: PropTypes.number,
    })
  ).isRequired,
  onItemSelected: PropTypes.func.isRequired,
  onDownload: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
  onDialogOpen: PropTypes.func.isRequired,
  totalPower: PropTypes.number,
  onSelect: PropTypes.func.isRequired,
};
