import PropTypes from "prop-types";
import { useState } from "react";
import { styled, useTheme } from "@mui/material/styles";
import Box from "@mui/material/Box";
import MuiDrawer from "@mui/material/Drawer";
import CollapseIcon from "../../../utils/icons/CollapseIcon";
import PowermapInfo from "./PowermapInfo";
import HeatmapInfo from "./HeatmapInfo";
import RealTimeDataIcon from "../../../utils/icons/RealTimeDataIcon";
import MapDimensionsIcon from "../../../utils/icons/MapDimensionsIcon";
import Tooltip, { tooltipClasses } from "@mui/material/Tooltip";

// import { useTTVWSContext } from "../../../api/webSocketService";

const drawerWidth = 250;

const openedMixin = (theme) => ({
  width: drawerWidth,
  transition: theme.transitions.create("width", {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.enteringScreen,
  }),
  overflowX: "hidden",
  backgroundColor: "#f9fafb",
  borderColor: "#d0d5dd",
});

const closedMixin = (theme) => ({
  transition: theme.transitions.create("width", {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  overflowX: "hidden",
  width: `calc(${theme.spacing("3rem")} + 1px)`,
  [theme.breakpoints.up("sm")]: {
    width: `calc(${theme.spacing("3rem")} + 1px)`,
  },
  backgroundColor: "#f9fafb",
});

const Drawer = styled(MuiDrawer, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ theme, open }) => ({
  width: drawerWidth,
  flexShrink: 0,
  whiteSpace: "nowrap",
  boxSizing: "border-box",
  ...(open && {
    ...openedMixin(theme),
    "& .MuiDrawer-paper": openedMixin(theme),
  }),
  ...(!open && {
    ...closedMixin(theme),
    "& .MuiDrawer-paper": closedMixin(theme),
  }),
}));

const CustomTooltip = styled(({ className, ...props }) => (
  <Tooltip {...props} classes={{ popper: className }} />
))(() => ({
  [`& .${tooltipClasses.tooltip}`]: {
    backgroundColor: "#344054",
    color: "#fff",
    fontSize: 14,
  },
}));

export default function RightSidebarComponent({ heatmapDimensions }) {
  const theme = useTheme();
  const [open, setOpen] = useState(false);

  const handleDrawerToggle = () => {
    setOpen((prevOpen) => !prevOpen);
  };

  // Handle undefined heatmapDimensions
  const { rows = 17, cols = 13 } = heatmapDimensions || {};

  return (
    <div className="z-index-0" data-testid="right-side-bar">
      <Box sx={{ display: "flex", flexDirection: "column", zIndex: 0 }}>
        <Box sx={{ flexShrink: 0 }} />
        <Box sx={{ display: "flex", flexGrow: 1 }}>
          <Drawer variant="permanent" anchor="right" open={open}>
            <Box
              sx={{ display: "flex", flexDirection: "column", height: "100%" }}
            >
              <Box sx={{ flexGrow: 1 }}>
                {open ? (
                  <div
                    className="right-sidebar gap-3"
                    style={{ padding: "0.75rem 1rem" }}
                  >
                    <PowermapInfo />
                    <HeatmapInfo rows={rows} cols={cols} />
                  </div>
                ) : (
                  <div
                    className="d-flex flex-column align-items-center w-100 right-sidebar"
                    style={{ gap: "0.3rem", padding: "0.75rem 1rem" }}
                  >
                    <CustomTooltip title="Real time data">
                      <div onClick={handleDrawerToggle}>
                        <RealTimeDataIcon />
                      </div>
                    </CustomTooltip>
                    <CustomTooltip title="Map dimensions">
                      <div onClick={handleDrawerToggle}>
                        <MapDimensionsIcon />
                      </div>
                    </CustomTooltip>
                  </div>
                )}
              </Box>
              <Box
                sx={{
                  display: "flex",
                  justifyContent: "start",
                  padding: theme.spacing(1),
                }}
              >
                <CustomTooltip title={open ? "" : "Expand"}>
                  <div
                    className="collapse-box d-flex gap-3 align-items-center w-100 cursor-pointer"
                    onClick={handleDrawerToggle}
                  >
                    <div
                      className="collapse-icon d-flex align-items-center h-1-125 w-100 gap-3"
                      data-testid="collapse"
                    >
                      <CollapseIcon reversed={!open} />
                      <span className="fs-6 fw-500 secondary-text-color">
                        Collapse
                      </span>
                    </div>
                  </div>
                </CustomTooltip>
              </Box>
            </Box>
          </Drawer>
        </Box>
      </Box>
    </div>
  );
}

RightSidebarComponent.propTypes = {
  heatmapDimensions: PropTypes.shape({
    rows: PropTypes.number,
    cols: PropTypes.number,
  }).isRequired,
};
