import PropTypes from "prop-types";
import { Dialog } from "@mui/material";
import CloseIcon from "../../icons/CloseIcon";
import { dialogCommonProps } from "../../dialogCommonProps";

export default function RemoveAllCellsDialog({ open, onClose, onConfirm }) {
  if (!open) return null;

  return (
    <Dialog
      open={open}
      fullWidth
      slotProps={{ backdrop: { style: { opacity: 0.3 } } }}
      PaperProps={dialogCommonProps}
      maxWidth="sm"
    >
      <div className="d-flex justify-content-end">
        <CloseIcon onClick={onClose} />
      </div>

      <div className="d-flex flex-column gap-4">
        <div className="d-flex flex-column gap-3">
          <span
            className="title"
            style={{ width: "21rem", alignSelf: "center" }}
          >
            Are you sure you want to delete all selected cells?
          </span>
        </div>

        <div className="d-flex justify-content-center flex-column gap-2">
          <button
            onClick={onClose}
            className="bg-transparent border-0 cancel-delete fs-6 fw-500"
            data-testid="cancel-delete"
          >
            No, I changed my mind
          </button>
          <button
            onClick={onConfirm}
            className="bg-transparent border-0 confirm-delete fs-6 fw-500"
          >
            Delete all selected cells
          </button>
        </div>
      </div>
    </Dialog>
  );
}

RemoveAllCellsDialog.propTypes = {
  open: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  onConfirm: PropTypes.func.isRequired,
};
