import PropTypes from "prop-types";
import { Popover, List, ListItemText, ListItemButton } from "@mui/material";

export default function FileOptionsPopover({
  anchorEl,
  open,
  onClose,
  onDownload,
  onDelete,
}) {
  const id = open ? "simple-popover" : undefined;

  return (
    <Popover
      id={id}
      open={open}
      anchorEl={anchorEl}
      onClose={onClose}
      anchorOrigin={{ vertical: "bottom", horizontal: "center" }}
      transformOrigin={{ vertical: "top", horizontal: "center" }}
      classes={{ paper: "popover-paper" }}
      slotProps={{
        paper: {
          sx: { width: "11.3rem" },
        },
      }}
    >
      <List sx={{ padding: 0 }}>
        <ListItemButton
          disableRipple
          className="list-item-button"
          onClick={onDownload}
        >
          <div className="d-flex align-items-center gap-3">
            <ListItemText primary="Download" className="MuiTypography-root" />
          </div>
        </ListItemButton>
        <ListItemButton
          disableRipple
          className="list-item-button"
          onClick={onDelete}
        >
          <div className="d-flex align-items-center gap-3">
            <ListItemText primary="Delete" className="MuiTypography-root" />
          </div>
        </ListItemButton>
      </List>
    </Popover>
  );
}

FileOptionsPopover.propTypes = {
  anchorEl: PropTypes.object,
  open: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  onDownload: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
};
