export const FILE_KEY = "experimentFiles";

export const getFile = (key) => {
  const storedFiles = localStorage.getItem(FILE_KEY);
  const files = storedFiles ? JSON.parse(storedFiles) : {};
  return files[key] || "";
};

export function getFiles() {
  const files = localStorage.getItem(FILE_KEY);
  return files ? JSON.parse(files) : {};
}

export function deleteFile(fileName) {
  const files = getFiles();
  delete files[fileName];
  localStorage.setItem(FILE_KEY, JSON.stringify(files));
}

export const handleFileChange = (
  event,
  acceptedFormats,
  setError,
  setUploadProgress,
  setUploadedFileName,
  setFileUploaded
  // uploadInProgress
) => {
  const file = event.target.files[0];
  const acceptedTypes = acceptedFormats
    .split(",")
    .map((format) => format.trim().toLowerCase());

  if (file && acceptedTypes.includes(`.${file.name.split(".").pop()}`)) {
    setError("");
    setUploadProgress(0); // Reset progress
    setUploadedFileName(null); // Clear previous file name
    setFileUploaded(false); // Reset file uploaded state
    localStorage.removeItem("fileUploaded"); // Remove from localStorage

    // Start simulating file upload progress
    // uploadInProgress(file);
  } else {
    setError(`Invalid file type. Only ${acceptedFormats} allowed.`);
  }
};

export function parseTTVData(data, isJSON = false) {
  if (typeof data !== "string") {
    console.error("Expected data to be a string but got:", typeof data);
    return [];
  }

  if (isJSON) {
    try {
      const jsonData = JSON.parse(data);
      return Object.values(jsonData)
        .map((item) => {
          if (typeof item.x !== "number" || typeof item.y !== "number") {
            console.error("Invalid coordinate type:", item);
            return null;
          }
          return {
            coordinate: { x: item.x, y: item.y },
            temperature:
              item.temperature !== undefined ? item.temperature : null,
          };
        })
        .filter((item) => item !== null);
    } catch (error) {
      console.error("Error parsing JSON data:", error);
      return [];
    }
  } else {
    const lines = data
      .split("\n")
      .filter((line) => line && !line.startsWith("#"));

    return lines
      .map((line) => {
        const [coordinate, temperature] = line.split(/\s+/);
        const [x, y] = coordinate.match(/\d+/g).map(Number);
        if (typeof x !== "number" || typeof y !== "number") {
          console.error("Invalid coordinate type:", coordinate);
          return null;
        }
        return { coordinate: { x, y }, temperature: parseFloat(temperature) };
      })
      .filter((item) => item !== null);
  }
}
