import PropTypes from "prop-types";

export default function TopNavbar({
  isHeatmap,
  setIsHeatmap,
  disabled = false,
}) {
  return (
    <div className="navbar-slider-container d-flex align-items-center justify-content-center">
      <div className="switch-toggle">
        <input
          className="switch-toggle-checkbox"
          type="checkbox"
          id="pricing-plan-switch"
          checked={isHeatmap}
          onChange={() => setIsHeatmap(!isHeatmap)}
          disabled={disabled}
        />
        <label className="switch-toggle-label" htmlFor="pricing-plan-switch">
          <span className="d-flex align-items-center gap-2"></span>
          <span className="d-flex align-items-center gap-2"></span>
          <div className="switch-toggle-knob" />
        </label>
      </div>
    </div>
  );
}

TopNavbar.propTypes = {
  isHeatmap: PropTypes.bool.isRequired,
  setIsHeatmap: PropTypes.func.isRequired,
  disabled: PropTypes.bool,
  isOnline: PropTypes.bool.isRequired,
};
