import { useEffect, useState, useRef, useCallback, useMemo } from "react";
import PropTypes from "prop-types";
import chroma from "chroma-js";
import GradientScale from "./PowermapGradientScale";
import HeatFluxInfo from "../../Sidebars/RightSidebar/HeatFluxInfo";
import DetailInfo from "../../Sidebars/RightSidebar/DetailInfo";
import CustomTooltip from "../../../utils/components/CustomTooltip";
import AreaIcon from "../../../utils/icons/AreaIcon";
import PowerIcon from "../../../utils/icons/PowerIcon";
import {
  formatHeatFlux,
  formatPower,
  generateColorScale,
  containerStyle,
  cellStyle,
  initializeColorScale,
  horizontalDimensionStyle,
  verticalDimensionStyle
} from "../../../utils/heatFluxHelper";
import CircularProgress from "@mui/material/CircularProgress";
import debounce from "lodash/debounce";

const LOCAL_STORAGE_KEY = "powermapData";
export default function PowermapComponent({ data, onValuesChange }) {
  const [heatmapCells, setHeatmapCells] = useState([]);
  const [colorScale, setColorScale] = useState(initializeColorScale);
  const containerRef = useRef(null);
  const [totalPower, setTotalPower] = useState(0);
  const [minVal, setMinVal] = useState(0);
  const [avgVal, setAvgVal] = useState(0);
  const [maxVal, setMaxVal] = useState(0);
  const [formattedMinVal, setFormattedMinVal] = useState("");
  const [formattedAvgVal, setFormattedAvgVal] = useState("");
  const [formattedMaxVal, setFormattedMaxVal] = useState("");
  const [loading, setLoading] = useState(true);

  const validateStoredData = (data) => {
    return (
      data &&
      Array.isArray(data.heatmapCells) &&
      typeof data.colorScale === "object" &&
      typeof data.totalPower === "number" &&
      typeof data.minVal === "number" &&
      typeof data.avgVal === "number" &&
      typeof data.maxVal === "number" &&
      typeof data.formattedMinVal === "string" &&
      typeof data.formattedAvgVal === "string" &&
      typeof data.formattedMaxVal === "string"
    );
  };

  const loadFromLocalStorage = useCallback(() => {
    try {
      const storedData = localStorage.getItem(LOCAL_STORAGE_KEY);
      if (storedData) {
        const parsedData = JSON.parse(storedData);
        if (validateStoredData(parsedData)) {
          setHeatmapCells(parsedData.heatmapCells || []);
          setColorScale(() =>
            chroma.scale(parsedData.colorScale).domain([0, 1])
          );
          setTotalPower(parsedData.totalPower || 0);
          setMinVal(parsedData.minVal || 0);
          setAvgVal(parsedData.avgVal || 0);
          setMaxVal(parsedData.maxVal || 0);
          setFormattedMinVal(parsedData.formattedMinVal || "");
          setFormattedAvgVal(parsedData.formattedAvgVal || "");
          setFormattedMaxVal(parsedData.formattedMaxVal || "");
        } else {
          console.warn(
            "Stored data is missing some expected fields or has invalid structure."
          );
        }
      }
    } catch (error) {
      console.warn("Failed to parse local storage data:", error);
    } finally {
      setLoading(false);
    }
  }, []);

  const saveToLocalStorage = useCallback(
    debounce(() => {
      if (typeof localStorage === "undefined") {
        console.warn("localStorage is not available.");
        return;
      }
      try {
        const dataToSave = {
          heatmapCells,
          colorScale: colorScale.colors(),
          totalPower,
          minVal,
          avgVal,
          maxVal,
          formattedMinVal,
          formattedAvgVal,
          formattedMaxVal,
        };
        localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(dataToSave));
      } catch (error) {
        console.warn("Failed to save to local storage:", error);
      }
    }, 1000),
    [
      colorScale,
      heatmapCells,
      totalPower,
      minVal,
      avgVal,
      maxVal,
      formattedMinVal,
      formattedAvgVal,
      formattedMaxVal,
    ]
  );

  const validateDataArray = (data) => {
    return (
      Array.isArray(data) &&
      data.every(
        (row) =>
          Array.isArray(row) && row.every((value) => !isNaN(Number(value)))
      )
    );
  };

  const debouncedProcessData = useCallback(
    debounce(() => {
      if (!Array.isArray(data) || data.length === 0) {
        console.warn("No data available for processing");
        return;
      }

      if (!validateDataArray(data)) {
        console.warn("Data array is not in the expected format");
        return;
      }

      setLoading(true);

      // Usar los datos directamente ya que la primera fila ya fue excluida en sendPowermap
      const processedData = data;
      if (processedData.length === 0 || processedData[0].length === 0) {
        console.warn("Processed data is empty");
        setLoading(false);
        return;
      }

      try {
        const allValues = processedData.flat().map((value) => Number(value));
        const minVal = Math.min(...allValues);
        const maxVal = Math.max(...allValues);
        const total = allValues.reduce((acc, value) => acc + value, 0);
        const avgVal = total / allValues.length;

        setTotalPower(total);
        setMinVal(minVal);
        setAvgVal(avgVal);
        setMaxVal(maxVal);
        setFormattedMinVal(formatHeatFlux(minVal));
        setFormattedAvgVal(formatHeatFlux(avgVal));
        setFormattedMaxVal(formatHeatFlux(maxVal));

        const newColorScale = generateColorScale(minVal, maxVal);
        setColorScale(() => newColorScale);

        const sectionAverages = processedData.map(
          (row) =>
            row.reduce((acc, value) => acc + Number(value), 0) / row.length
        );
        if (onValuesChange) onValuesChange(sectionAverages, newColorScale);

        const cells = processedData.map((row, rowIndex) =>
          row.map((value, colIndex) => ({
            value: Number(value),
            row: rowIndex,
            col: colIndex,
            color: newColorScale(Number(value)).hex(),
          }))
        );
        setHeatmapCells(cells);

        saveToLocalStorage();
      } catch (error) {
        console.warn("Failed to process data:", error);
      } finally {
        setLoading(false);
      }
    }, 500),
    [data, onValuesChange, saveToLocalStorage]
  );

  useEffect(() => {
    loadFromLocalStorage();
  }, [loadFromLocalStorage]);

  useEffect(() => {
    if (data && data.length) {
      debouncedProcessData();
    }
  }, [data, debouncedProcessData]);

  useEffect(() => {
    if (!containerRef.current) return;
  }, [heatmapCells]);

  const memoizedGradientScaleValues = useMemo(() => {
    return heatmapCells.flat().map((cell) => cell.value);
  }, [heatmapCells]);

  const numCols = heatmapCells[0]?.length || 0;
  const numRows = heatmapCells.length || 0;

  const unit_cell_dim = { get: (index) => (index === 0 ? 1 : 1) };

  // Calculate the area of one cell
  const cellWidth = unit_cell_dim.get(0) * 10; // Convert to cm
  const cellHeight = unit_cell_dim.get(1) * 10; // Convert to cm
  const cellArea = cellWidth * cellHeight; // Area in cm²

  // Calculate the total area in square meters
  const totalArea = (cellArea * numCols * numRows) / 10000; // Total area in m²

  return (
    <>
      {loading ? (
        <div className="loading-container d-flex justify-content-center h-100">
          <CircularProgress />
        </div>
      ) : (
        <>
          <div className="d-flex flex-column justify-content-start align-items-start w-35 gap-4">
            <span className="status-body-bold">Heat flux</span>
            <div
              className="heat-flux d-flex flex-column"
              style={{ gap: "2rem" }}
            >
              <HeatFluxInfo
                label="Lowest"
                value={formattedMinVal}
                color={colorScale(minVal).hex()}
              />
              <HeatFluxInfo
                label="Average"
                value={formattedAvgVal}
                color={colorScale(avgVal).hex()}
              />
              <HeatFluxInfo
                label="Peak"
                value={formattedMaxVal}
                color={colorScale(maxVal).hex()}
              />
            </div>
          </div>
          <div style={{ position: "relative", width: "100%", height: "100%" }}>
            <div className="powermap d-flex justify-content-center position-relative" style={{ padding: "2rem 3rem 3rem 2rem" }}>
              <div
                ref={containerRef}
                style={{
                  ...containerStyle,
                  gridTemplateColumns: `repeat(${numCols}, 2rem)`,
                  gridTemplateRows: `repeat(${numRows}, 2rem)`,
                  gap: "1px",
                }}
              >
                {heatmapCells.flat().map((cell, index) => (
                  <CustomTooltip
                    key={index}
                    title={formatHeatFlux(cell.value)}
                    placement="top"
                  >
                    <div
                      style={{
                        ...cellStyle,
                        backgroundColor: cell.color,
                      }}
                    />
                  </CustomTooltip>
                ))}
              </div>

              {/* Dimensión vertical */}
              <div style={verticalDimensionStyle}>
                {numRows} mm
              </div>

              {/* Dimensión horizontal */}
              <div style={horizontalDimensionStyle}>
                {numCols} mm
              </div>
            </div>
          </div>
          <div className="d-flex flex-column justify-content-start align-items-end w-35 gap-4">
            <span className="status-body-bold">Details</span>
            <div
              className="heat-flux d-flex flex-column"
              style={{ gap: "2rem" }}
            >
              <DetailInfo
                label="Area"
                value={
                  <>
                    {`${totalArea.toFixed(2)} `}
                    <span style={{ fontSize: "0.85rem" }}>
                      cm<sup>2</sup>
                    </span>
                  </>
                }
                Icon={AreaIcon}
              />
              <DetailInfo
                label="Total power"
                value={formatPower(totalPower)}
                Icon={PowerIcon}
              />
            </div>
          </div>
          <GradientScale
            values={memoizedGradientScaleValues}
            colorScale={colorScale}
            numStops={4}
            scale={1}
          />
        </>
      )}
    </>
  );
}

PowermapComponent.propTypes = {
  data: PropTypes.arrayOf(
    PropTypes.arrayOf(PropTypes.oneOfType([PropTypes.string, PropTypes.number]))
  ).isRequired,
  onValuesChange: PropTypes.func,
};
