import { useState } from "react";
import PropTypes from "prop-types";
import { styled } from "@mui/material/styles";
import Box from "@mui/material/Box";
import MuiDrawer from "@mui/material/Drawer";
// import CollapseIcon from "../../../utils/icons/CollapseIcon";
import UploadCellmapComponent from "../../UploadFiles/UploadCellmapFile/UploadCellmapComponent";
import UploadCalibrationFileComponent from "../../UploadFiles/UploadCalibrationFile/UploadCalibrationFileComponent";
import UploadPowermapComponent from "../../UploadFiles/UploadPowermapFile/UploadPowermapComponent";
import RTDSettings from "./RTDSettings";
import { useTTVWSContext } from "../../../api/webSocketService";
// import PlayIcon from "../../../utils/icons/PlayIcon";
// import StopIcon from "../../../utils/icons/StopIcon";
import { CustomTooltip } from "../../../utils/dialogCommonProps";
import SaveFileDialog from "../../../utils/components/Dialogs/SaveFileDialog";
import TopNavbar from "../../Header/TopNavbar";
import IPStatus from "../../Header/IPStatus";
import WarningAlert from "../../../utils/components/WarningAlert";

const drawerWidth = 300;

const openedMixin = (theme) => ({
  width: drawerWidth,
  transition: theme.transitions.create("width", {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.enteringScreen,
  }),
  overflowX: "hidden",
  backgroundColor: "#f9fafb",
  borderColor: "#d0d5dd",
});

const closedMixin = (theme) => ({
  transition: theme.transitions.create("width", {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  overflowX: "hidden",
  width: `calc(${theme.spacing("3rem")} + 1px)`,
  [theme.breakpoints.up("sm")]: {
    width: `calc(${theme.spacing("3rem")} + 1px)`,
  },
  backgroundColor: "#f9fafb",
});

const Drawer = styled(MuiDrawer, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ theme, open }) => ({
  width: drawerWidth,
  flexShrink: 0,
  whiteSpace: "nowrap",
  boxSizing: "border-box",
  ...(open && {
    ...openedMixin(theme),
    "& .MuiDrawer-paper": openedMixin(theme),
  }),
  ...(!open && {
    ...closedMixin(theme),
    "& .MuiDrawer-paper": closedMixin(theme),
  }),
}));

export default function LeftSidebarComponent({
  onFileUpload,
  onDimensionsChange,
  setIsHeatmap,
  isHeatmap,
}) {
  const [drawerOpen, setDrawerOpen] = useState(true);
  const [cellmapUploaded, setCellmapUploaded] = useState(false);
  const [ttvUploaded, setTtvUploaded] = useState(false);
  const [powermapUploaded, setPowermapUploaded] = useState(false);
  const [powermapSelected, setPowermapSelected] = useState(false);
  const [alertMessage, setAlertMessage] = useState(null);
  const [showSaveFileDialog, setShowSaveFileDialog] = useState(false);
  const [networkError, setNetworkError] = useState(false);

  const {
    isSimulationStarted,
    isStreamingStarted,
    startSimulation,
    stopSimulation,
    startStreaming,
    stopStreaming,
    recordFileUrl,
    timeStamp,
    message,
    showErrorDialog
  } = useTTVWSContext();

  const handleDrawerToggle = () => {
    setDrawerOpen((prev) => !prev);
  };

  const handleFileUpload = (setUploadState, fileContent) => {
    console.log('Uploading file with content:', fileContent);
    if (typeof setUploadState === 'function') {
      setUploadState(true);
    }
    if (onFileUpload) {
      onFileUpload(fileContent);
    }
    setCellmapUploaded(true);
  };

  const handleFileDelete = (setUploadState) => {
    if (typeof setUploadState === 'function') {
      setUploadState(false);
    }
    setCellmapUploaded(false);
  };

  const handlePowermapFileUpload = (isUploaded) => {
    setPowermapUploaded(isUploaded);
    if (!isUploaded && isSimulationStarted) {
      stopSimulation();
    }
  };

  const handlePowermapSelectionChange = (isSelected) => {
    setPowermapSelected(isSelected);
  };

  const filesUploaded = cellmapUploaded && ttvUploaded && powermapUploaded;
  const isValidToStartSimulation = filesUploaded && powermapSelected;

  const handleStartStreaming = (e) => {
    e.preventDefault();
    setAlertMessage(null);
    if (isValidToStartSimulation) {
      if (isStreamingStarted) {
        stopStreaming();
      } else {
        startStreaming();
      }
    } else {
      setAlertMessage(
        "Please upload all required files and select a powermap to start recording the streaming."
      );
      setTimeout(() => setAlertMessage(null), 5000);
    }
  };

  // const handleStartRecording = async (e) => {
  //   e.preventDefault();
  //   setAlertMessage(null);

  //   if (!isValidToStartSimulation && !isSimulationStarted) {
  //     setAlertMessage(
  //       "Please upload all required files and select a powermap to start recording the streaming."
  //     );
  //     setTimeout(() => setAlertMessage(null), 5000);
  //   } else if (isSimulationStarted) {
  //     setShowSaveFileDialog(true);
  //   } else {
  //     try {
  //       if (isStreamingStarted) {
  //         stopStreaming();
  //       }
  //       startSimulation();
  //     } catch (error) {
  //       console.error("Error starting simulation:", error);
  //       setAlertMessage("An error occurred while starting the simulation.");
  //       setTimeout(() => setAlertMessage(null), 5000);
  //     }
  //   }
  // };

  const handleCloseMessage = () => {
    setAlertMessage(null);
  };

  const handleSaveAndContinue = (e) => {
    e.preventDefault();
    setShowSaveFileDialog(false);
    startSimulation();
  };

  const handleSaveAndStartNew = (e) => {
    e.preventDefault();
    setShowSaveFileDialog(false);
    stopSimulation();
  };

  const handleDialogClose = () => {
    setShowSaveFileDialog(false);
  };

  const elapsedTimeInSeconds = timeStamp ?? 0;
  const minutes = Math.floor(elapsedTimeInSeconds / 60);
  const seconds = Math.floor(elapsedTimeInSeconds % 60);

  const formattedTime = `${String(minutes).padStart(2, "0")}:${String(seconds).padStart(2, "0")}`;

  // const showRecordFile = !!recordFileUrl;

  const checkUploadedFile =
    cellmapUploaded && ttvUploaded && powermapUploaded && !networkError;

  return (
    <div>
      {showErrorDialog && message && (
        <div className="error-message">
          {message}
        </div>
      )}
      <div className="z-index-0" data-testid="left-side-bar">
        <Box sx={{ display: "flex", flexDirection: "column", zIndex: 0 }}>
          <Box sx={{ display: "flex", flexGrow: 1 }}>
            <Drawer
              variant="permanent"
              anchor="left"
              open={drawerOpen}
              sx={{
                '& .MuiDrawer-paper': {
                  backgroundColor: '#f9fafb',
                  transition: 'all 0.3s ease-in-out',
                }
              }}
            >
              <Box
                sx={{
                  display: "flex",
                  flexDirection: "column",
                  height: "100%",
                  position: "relative",
                }}
              >
                <IPStatus drawerOpen={drawerOpen} />
                <Box sx={{ flexGrow: 1, overflow: "auto" }}>
                  <div className="left-sidebar" style={{ padding: "0.75rem 1rem" }}>
                    {drawerOpen && (
                      <>
                        <div className="d-flex flex-column gap-3">
                          <UploadCellmapComponent
                            acceptedFormats=".json"
                            onFileUpload={(fileContent) =>
                              handleFileUpload(setCellmapUploaded, fileContent)
                            }
                            onFileDelete={() => handleFileDelete(setCellmapUploaded)}
                            onDimensionsChange={onDimensionsChange}
                            setNetworkError={setNetworkError}
                          />

                          {cellmapUploaded && !networkError && (
                            <UploadCalibrationFileComponent
                              acceptedFormats=".json"
                              onFileUpload={(fileContent) =>
                                handleFileUpload(setTtvUploaded, fileContent)
                              }
                              onFileDelete={() => handleFileDelete(setTtvUploaded)}
                            />
                          )}

                          {cellmapUploaded && ttvUploaded && (
                            <UploadPowermapComponent
                              onFileUpload={handlePowermapFileUpload}
                              onFileDelete={() =>
                                handleFileDelete(setPowermapUploaded)
                              }
                              onPowermapSelectionChange={
                                handlePowermapSelectionChange
                              }
                            />
                          )}

                          {cellmapUploaded && ttvUploaded && powermapUploaded && (
                            <RTDSettings
                              onSave={() => setIsHeatmap(false)}
                              onAdd={() => setIsHeatmap(true)}
                            />
                          )}

                          <WarningAlert
                            show={!!alertMessage}
                            message={alertMessage}
                            onClose={handleCloseMessage}
                          />
                        </div>
                      </>
                    )}
                  </div>
                </Box>
                <Box
                  sx={{
                    display: "flex",
                    justifyContent: "start",
                    padding: "8px",
                  }}
                >
                  <CustomTooltip title={drawerOpen ? "" : "Expand"}>
                    <div
                      className="collapse-box d-flex gap-3 align-items-center w-100 cursor-pointer"
                      onClick={handleDrawerToggle}
                    >
                      {/* <div
                        className="collapse-icon d-flex align-items-center h-1-125 w-100 gap-3"
                        data-testid="collapse"
                      >
                        <CollapseIcon reversed={!drawerOpen} />
                        {drawerOpen && (
                          <span className="fs-6 fw-500 secondary-text-color">
                            Collapse
                          </span>
                        )}
                      </div> */}
                    </div>
                  </CustomTooltip>
                </Box>
              </Box>
            </Drawer>

            <div className="interactions-bar-container d-flex align-items-end" style={{ height: "100vh" }}>
              <div className="h-100 interactions-bar">
                {checkUploadedFile && !networkError && (
                  <div className="d-flex flex-column gap-3 h-100">
                    <div className="top-navbar d-flex justify-content-between align-items-center z-index-1">
                      <TopNavbar
                        isHeatmap={isHeatmap}
                        setIsHeatmap={setIsHeatmap}
                        disabled={!navigator.onLine}
                        isOnline={navigator.onLine}
                      />

                      {/* <div
                        onClick={handleStartRecording}
                        className={`d-flex align-items-center ${!isValidToStartSimulation && !isSimulationStarted ? "disabled" : ""}`}
                      >
                        <span className="d-flex cursor-pointer align-items-center gap-2">
                          {isSimulationStarted ? (
                            <CustomTooltip title="Stop Recording">
                              <div className="stop-icon">
                                <StopIcon />
                              </div>
                            </CustomTooltip>
                          ) : (
                            <CustomTooltip title="Record">
                              <div
                                className={`play-icon ${!isValidToStartSimulation && !isSimulationStarted ? "disabled-btn" : ""}`}
                              >
                                <PlayIcon />
                              </div>
                            </CustomTooltip>
                          )}
                        </span>
                      </div> */}
                      <div className="d-flex justify-content-between w-100">
                        {isStreamingStarted ? (
                          <div className="power-off">
                            <button
                              className="border-0 bg-transparent"
                              onClick={handleStartStreaming}
                            >
                              <span className="h-1-125 fw-500 fs-0-875 lh-1">
                                Off
                              </span>
                            </button>
                          </div>
                        ) : (
                          <div className="power-on">
                            <button
                              className={`border-0 bg-transparent ${!isValidToStartSimulation && !isStreamingStarted ? "disabled" : ""}`}
                              onClick={handleStartStreaming}
                            >
                              <span className="h-1-125 fw-500 fs-0-875 lh-1">
                                On
                              </span>
                            </button>
                          </div>
                        )}
                      </div>
                      {isSimulationStarted && elapsedTimeInSeconds !== null && (
                        <div className="record-box">
                          <span
                            className="recording-timestamp fs-0-74 fw-400"
                            style={{ color: "#344054" }}
                          >
                            {formattedTime}
                          </span>
                        </div>
                      )}
                    </div>
                  </div>
                )}
              </div>
            </div>
          </Box>
        </Box>
        <SaveFileDialog
          open={showSaveFileDialog}
          onClose={handleDialogClose}
          onSaveAndContinue={handleSaveAndContinue}
          onSaveAndStartNew={handleSaveAndStartNew}
          recordFileUrl={recordFileUrl}
        />
      </div>
    </div>
  );
}

LeftSidebarComponent.propTypes = {
  onFileUpload: PropTypes.func,
  onDimensionsChange: PropTypes.func,
  setIsHeatmap: PropTypes.func,
  isHeatmap: PropTypes.bool.isRequired,
};