import { useState } from "react";
import QuestionMarkIcon from "../../icons/QuestionMarkIcon";
import { Popover } from "@mui/material";
import PropTypes from "prop-types";

/**
 * QuestionPopOver
 * @param {object} props
 * @param {object} props.style
 * @param {string} props.text
 * @param {string} props.coloricon
 * @returns {JSX.Element}
 * @constructor
 * @example
 * <QuestionPopOver
 * style={{ width: "2.00rem", height: "2.00rem" }}
 * text="Lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed non risus. Suspendisse lectus tortor, dignissim sit amet, adipiscing nec, ultricies sed, dolor."
 * coloricon="#FCFCFD"
 * />
 */
export default function QuestionPopOver(props, { onClick }) {
  const [anchorEl, setAnchorEl] = useState(null);
  const handleClick = (event) => {
    event.stopPropagation();
    setAnchorEl(event.currentTarget);
    if (onClick) {
      onClick(event);
    }
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);
  const id = open ? "simple-popover" : undefined;

  return (
    <>
      <QuestionMarkIcon
        onClick={handleClick}
        popoveropen={open ? 1 : 0}
        style={{ width: "2.00rem", height: "2.00rem", ...props.style }}
        coloricon={props.coloricon}
      />
      <Popover
        onClose={(event) => {
          event.stopPropagation();
          handleClose();
        }}
        open={open}
        id={id}
        anchorEl={anchorEl}
        anchorOrigin={{
          vertical: "top",
          horizontal: "right",
        }}
        data-testid="popover"
        transformOrigin={{
          vertical: "top",
          horizontal: "left",
        }}
        sx={{
          "& .MuiPaper-root": {
            maxWidth: "20rem",
            padding: "0.5rem 1rem",
            marginLeft: "0.6666666667rem",
            color: "#FCFCFD",
            flexShrink: "0",
            borderRadius: "0.4166666667rem",
            background: "#344054",
            boxShadow: "none",
          },
        }}
      >
        <p
          className="fs-0-74 m-0"
          style={{
            color: "#FCFCFD",
            textWrap: "wrap",
            whiteSpace: "initial",
            fontWeight: 400,
          }}
        >
          {props.text}
        </p>
      </Popover>
    </>
  );
}

QuestionPopOver.propTypes = {
  style: PropTypes.object,
  text: PropTypes.any,
  coloricon: PropTypes.string,
};
