import PropTypes from "prop-types";
import { useState, useEffect } from "react";
import DropDownOpenIcon from "../../../utils/icons/DropDownOpenIcon";
import DropDownCloseIcon from "../../../utils/icons/DropDownCloseIcon";
import chroma from "chroma-js";
import HorizonlArrowIcon from "../../../assets/icons/horizontalArrow.svg";
import VerticalArrowIcon from "../../../assets/icons/verticalArrow.svg";
import MapDimensionsIcon from "../../../utils/icons/MapDimensionsIcon";
import WidthIcon from "../../../utils/icons/WidthIcon";
import HeightIcon from "../../../utils/icons/HeightIcon";

export function generateHeatmapData(parsedData = []) {
  const heatmapData = [];
  parsedData.forEach(({ coordinate, temperature }) => {
    if (
      typeof coordinate === "object" &&
      coordinate !== null &&
      "x" in coordinate &&
      "y" in coordinate
    ) {
      const coordinateStr = `${String.fromCharCode(65 + coordinate.y)}${coordinate.x + 1}`;
      heatmapData.push({ coordinate: coordinateStr, temperature });
    } else {
      console.error("Invalid coordinate type:", coordinate);
    }
  });
  return heatmapData;
}

export function getColorForTemperature(temp, minTemp, maxTemp) {
  const scale = chroma
    .scale(["#00008B", "#00CED1", "#32CD32", "#FFD700", "#FF8C00", "#B22222"])
    .domain([minTemp, maxTemp]);
  return scale(temp).hex();
}

export default function HeatmapInfo({ rows = 17, cols = 13 }) {
  const [isActive, setIsActive] = useState(false);
  const [heatmapData, setHeatmapData] = useState([]);
  const [dimension, setDimension] = useState({ x: 0, y: 0 });

  // const { sendPowermap } = useTTVWSContext();

  const minTemp = 0;
  const maxTemp = 30;

  const cellSizePx = 32;
  const pxToMm = 0.264583;
  const lengthMm = (rows * cellSizePx * pxToMm).toFixed(2);
  const widthMm = (cols * cellSizePx * pxToMm).toFixed(2);

  const toggleDropdown = () => {
    setIsActive(!isActive);
  };

  useEffect(() => {
    // Sample data for testing
    const sampleData = Array.from({ length: 25 }, (_, index) => ({
      coordinate: { x: index % 5, y: Math.floor(index / 5) },
      temperature: Math.random() * (maxTemp - minTemp) + minTemp,
    }));
    const data = generateHeatmapData(sampleData);
    setHeatmapData(data);

    const powermapData = {
      powermap: {
        max_val: 9340000,
        series: Array(8).fill(Array(24).fill(0)),
        dimension: { x: 1, y: 1 },
        name: "test_powermap.csv",
      },
    };
    // console.log(powermapData);
    setDimension(powermapData.powermap.dimension);
  }, []);

  return (
    <div className="select-menu d-flex flex-column" data-testid="powermap-info">
      <div
        className="select d-flex align-items-center justify-content-between"
        onClick={toggleDropdown}
      >
        <div className="d-flex align-items-center">
          {isActive ? <DropDownOpenIcon /> : <DropDownCloseIcon />}
          <span
            className={`fs-6 fw-500 ${isActive ? "main-text-color" : "closed-text-color"}`}
          >
            Map dimensions
          </span>
        </div>
      </div>
      {isActive && (
        <div className="d-flex flex-column gap-3">
          <div
            className="options-list active w-100 rtd-count mt-3"
            style={{ padding: "0.6rem" }}
          >
            <div className="option">
              <div className="d-flex flex-column position-relative gap-2">
                <div className="position-relative d-flex gap-2">
                  <div
                    className="heatmap-grid"
                    style={{
                      display: "grid",
                      gridTemplateColumns: `repeat(5, 32px)`,
                      gap: "1px",
                      background: "#ffffff",
                    }}
                  >
                    {heatmapData
                      .slice(0, 25)
                      .map(({ coordinate, temperature }) => {
                        const color = getColorForTemperature(
                          temperature,
                          minTemp,
                          maxTemp
                        );
                        return (
                          <div
                            key={coordinate}
                            style={{
                              backgroundColor: color,
                              width: "2rem",
                              height: "2rem",
                              display: "flex",
                              alignItems: "center",
                              justifyContent: "center",
                              fontSize: "10px",
                              color: "#000",
                              position: "relative",
                            }}
                          >
                            <span className="coordinate-label">
                              {coordinate}
                            </span>
                          </div>
                        );
                      })}
                  </div>
                  <div className="rows-rtd d-flex align-items-center">
                    <img src={VerticalArrowIcon} className="h-100" />
                    <span className="fs-0-875 lh-1-1 fw-400 main-text-color position-absolute">
                      {rows} RTD
                    </span>
                  </div>
                </div>
                <div className="cols-rtd d-flex flex-column justify-content-center align-items-center gap-2">
                  <img src={HorizonlArrowIcon} className="w-100" />
                  <span
                    className="fs-0-875 lh-1-1 fw-400 main-text-color"
                    style={{ right: "-2.2rem" }}
                  >
                    {cols} RTD
                  </span>
                </div>
              </div>
            </div>
          </div>
          <div className="d-flex flex-column align-items-center gap-4">
            <div className="d-flex flex-row w-100 align-items-center gap-3">
              <MapDimensionsIcon />
              <div className="d-flex flex-column">
                <span className="fs-6 fw-400 grey-text-color">Cell size:</span>
                <span className="fs-6 fw-400 secondary-text-color">
                  {dimension.x} mm x {dimension.y} mm
                </span>
              </div>
            </div>

            <div className="d-flex flex-row w-100 align-items-center gap-3">
              <HeightIcon />
              <div className="d-flex flex-column">
                <span className="fs-6 fw-400 grey-text-color">Length:</span>
                <span className="fs-6 fw-400 secondary-text-color">
                  {lengthMm} mm
                </span>
              </div>
            </div>

            <div className="d-flex flex-row w-100 align-items-center gap-3">
              <WidthIcon />
              <div className="d-flex flex-column">
                <span className="fs-6 fw-400 grey-text-color">Width:</span>
                <span className="fs-6 fw-400 secondary-text-color">
                  {widthMm} mm
                </span>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

HeatmapInfo.propTypes = {
  rows: PropTypes.number,
  cols: PropTypes.number,
};
