import { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { useTTVWSContext } from "../../api/webSocketService";
import RenderPlot from "./RenderPlot";

export default function PlotsContainer() {
  const { plots } = useTTVWSContext();
  const [isRunning, setIsRunning] = useState(false);

  useEffect(() => {
    const runningPlot = plots?.some((plot) => plot.isRunning);
    setIsRunning(runningPlot);
  }, [plots]);

  return (
    <div
      className={`plots-container d-flex flex-column justify-content-between w-100 h-100 gap-3 ${isRunning ? "is-running" : ""}`}
      data-testid="plots-container"
      style={{
        display: "grid",
        gridTemplateRows: `repeat(${plots?.length || 1}, 1fr)`,
        gap: "1rem",
        height: "100%",
        width: "100%",
        padding: "1rem",
        overflow: "hidden"
      }}
    >
      {plots?.map((plot, index) => (
        <RenderPlot key={index} plot={plot} index={index} />
      ))}
    </div>
  );
}

PlotsContainer.propTypes = {
  plots: PropTypes.array,
};
