export default function SaveFileDialog({
  open,
  onClose,
  // onSaveAndContinue,
  onSaveAndStartNew,
  recordFileUrl,
}) {
  if (!open) return null;

  async function downloadFileUrl(url) {
    const response = await fetch(url);
    if (!response.ok) {
      throw new Error('File download failed');
    }
    const blob = await response.blob();
    const downloadUrl = window.URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = downloadUrl;
    link.download = url.split('/').pop();
    link.click();
  }

  // const handleSaveAndContinue = async (e) => {
  //   e.preventDefault();
  //   if (recordFileUrl) {
  //     await downloadFileUrl(recordFileUrl);
  //   }
  //   if (onSaveAndContinue) onSaveAndContinue(e);
  // };

  const handleSaveAndStartNew = async (e) => {
    e.preventDefault();
    if (recordFileUrl) {
      await downloadFileUrl(recordFileUrl);
    }
    if (onSaveAndStartNew) onSaveAndStartNew(e);
  };

  return (
    <Dialog
      open={open}
      fullWidth
      slotProps={{ backdrop: { style: { opacity: 0.3, textAlign: "center" } } }}
      PaperProps={dialogCommonProps}
      maxWidth="sm"
    >
      <div className="d-flex justify-content-end">
        <CloseIcon onClick={onClose} />
      </div>

      <div className="d-flex flex-column align-items-center h-100 gap-3 mb-4">
        <span className="title" style={{ width: "22rem" }}>
          Do you want to stop and save recording?
        </span>
      </div>

      <div className="d-flex flex-column align-items-center gap-3">
        {/* Button to save and continue */}
        {/* <button
          onClick={handleSaveAndContinue}
          className="border-0 bg-transparent fs-6 fw-500 lh-1 text-decoration-none cursor-pointer"
          style={{ color: "#007D4E" }}
        >
          Save and continue experiment
        </button> */}

        {/* Button to save and start new */}
        <button
          onClick={handleSaveAndStartNew}
          className="border-0 bg-transparent fs-6 fw-500 lh-1 btn-grey text-decoration-none cursor-pointer"
          style={{ color: "#667085" }}
        >
          Save and start new one
        </button>
      </div>
    </Dialog>
  );
}


SaveFileDialog.propTypes = {
  open: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  onSaveAndStartNew: PropTypes.func.isRequired,
  recordFileUrl: PropTypes.any,
};
