import { useState } from "react";
import PropTypes from "prop-types";
import DropDownCloseIcon from "../../../utils/icons/DropDownCloseIcon";
import DropDownOpenIcon from "../../../utils/icons/DropDownOpenIcon";
import UploadIcon from "../../../utils/icons/UploadIcon";
import UploadedCellmapList from "./UploadedCellmapList";
import CheckIcon from "../../../utils/icons/CheckIcon";
import axios from "axios";
import DEFAULT_OPTIONS from "../../../utils/websocketConfig";
import QuestionPopOver from "../../../utils/components/PopOver/QuestionPopOver";
import WarningAlert from "../../../utils/components/WarningAlert";

const CELL_MAP_URL = `http://${DEFAULT_OPTIONS.host}:8080/cell_map`;

const validateCellMap = (data) => {
  const validateFile = `JSON file must be an object where each key is a string representation of a number, and each value is an object containing numeric 'x' and 'y' properties.`;
  if (typeof data !== "object" || Array.isArray(data)) {
    return validateFile;
  }

  const usedPositions = new Set();

  for (const key in data) {
    const item = data[key];
    if (typeof item !== "object" || item === null) {
      return "Each entry in the JSON file must be an object.";
    }
    if (!("x" in item) || !("y" in item)) {
      return "Each object in the JSON file must have 'x' and 'y' properties.";
    }
    if (typeof item.x !== "number" || typeof item.y !== "number") {
      return "The 'x' and 'y' properties must be numbers.";
    }

    // Check for duplicate positions
    const position = `${item.x},${item.y}`;
    if (usedPositions.has(position)) {
      return `Duplicate position found at x:${item.x}, y:${item.y}`;
    }
    usedPositions.add(position);
  }

  return null;
};

export default function UploadCellmapComponent({
  acceptedFormats,
  onFileUpload,
  onDimensionsChange,
  onFileDelete,
  setNetworkError,
}) {
  const [isActive, setIsActive] = useState(true);
  const [error, setError] = useState("");
  const [uploadedFileName, setUploadedFileName] = useState(null);
  const [fileUploaded, setFileUploaded] = useState(false);

  const toggleDropdown = () => setIsActive(!isActive);

  const handleFileChange = async (event) => {
    setError("");
    const selectedFile = event.target.files[0];
    if (
      selectedFile &&
      selectedFile.type === "application/json" &&
      selectedFile.name.endsWith(".json")
    ) {
      const reader = new FileReader();
      reader.onload = (e) => {
        const content = e.target.result;
        if (typeof content === "string") {
          processFileContent(selectedFile.name, content);
        } else {
          handleError(
            "Error reading file contents. Make sure the file is in the right format."
          );
        }
      };
      reader.onerror = () =>
        handleError(
          "Error reading file contents. Make sure the file is in the right format."
        );
      reader.readAsText(selectedFile);
      return;
    }
    handleError(
      "Invalid file format. Please upload a .json file with the right structure."
    );
  };

  const processFileContent = (fileName, content) => {
    try {
      const parsedData = JSON.parse(content);
      const validationError = validateCellMap(parsedData);
      if (validationError) {
        handleError(validationError);
        return;
      }

      // Convert the object data into an array format
      const processedData = Object.entries(parsedData).map(([key, value]) => ({
        id: parseInt(key),
        x: value.x,
        y: value.y
      }));

      // Sort by id to ensure correct order
      processedData.sort((a, b) => a.id - b.id);

      // Calculate actual dimensions based on the data
      const maxX = Math.max(...processedData.map(cell => cell.x));
      const minX = Math.min(...processedData.map(cell => cell.x));
      const maxY = Math.max(...processedData.map(cell => cell.y));
      const minY = Math.min(...processedData.map(cell => cell.y));

      // Calculate grid dimensions
      const dimensions = {
        cols: maxX - minX + 1,  // Number of columns
        rows: maxY - minY + 1,  // Number of rows
        totalCells: processedData.length,
        bounds: {
          minX,
          maxX,
          minY,
          maxY
        }
      };

      // Log detailed information for debugging
      console.log('Processed cell map data:', {
        totalCells: processedData.length,
        dimensions,
        gridSize: `${dimensions.rows} rows x ${dimensions.cols} columns`,
        bounds: `X: ${minX} to ${maxX}, Y: ${minY} to ${maxY}`,
        firstCell: processedData[0],
        lastCell: processedData[processedData.length - 1],
        uniqueX: [...new Set(processedData.map(cell => cell.x))].sort((a, b) => a - b),
        uniqueY: [...new Set(processedData.map(cell => cell.y))].sort((a, b) => a - b)
      });

      if (onFileUpload) {
        onFileUpload(processedData);
      }

      if (onDimensionsChange) {
        onDimensionsChange(dimensions);
      }

      // Send the original JSON string to maintain exact format
      axios
        .post(CELL_MAP_URL, content, {
          headers: { "Content-Type": "application/json" },
        })
        .then((response) => {
          if (response.status === 200) {
            setUploadedFileName(fileName);
            setFileUploaded(true);
            setNetworkError(false);
            setIsActive(false);
          } else {
            throw new Error(`HTTP error! status: ${response.status}`);
          }
        })
        .catch((error) => {
          if (error.message.includes("Network Error")) {
            handleError(
              "Unable to connect to the server. Please ensure the (TTV) is properly connected to the Raspberry Pi. If the TTV is already connected but the issue persists, try restarting the TTV. If the problem continues, please contact technical support."
            );
            setNetworkError(true);
          } else {
            handleError(`Error sending data: ${error.message}`);
            setNetworkError(false);
          }
        });
    } catch (error) {
      handleError(
        "Invalid JSON content. Make sure the content is well-formed JSON."
      );
    }
  };

  const handleError = (message) => {
    console.error("Cell map error:", message);
    setError(message);
    setFileUploaded(false);
  };

  const handleFileDelete = () => {
    setUploadedFileName(null);
    setFileUploaded(false);
    setError("");
    if (onFileDelete) {
      onFileDelete();
    }
  };


  return (
    <div
      className="select-menu d-flex flex-column w-100"
      data-testid="upload-cell-map"
    >
      <div
        className="select d-flex align-items-center justify-content-between"
        onClick={toggleDropdown}
      >
        <div className="d-flex align-items-center w-100">
          {isActive ? <DropDownOpenIcon /> : <DropDownCloseIcon />}
          <div className="d-flex align-items-center w-100 gap-2">
            <span
              className={`fs-6 fw-500 ${isActive ? "main-text-color" : "closed-text-color"}`}
            >
              {" "}
              Cell map file{" "}
            </span>
            <QuestionPopOver
              style={{ width: "1em", height: "1rem" }}
              text="Cell map defines the layout of the cells on the TTV. Cell map tells the application/software how the unit cells are arranged on the physical die."
            />
          </div>
        </div>
        {!isActive && fileUploaded && (
          <div className="check-icon-circle d-flex align-items-start">
            <CheckIcon size="1rem" />
          </div>
        )}
      </div>
      <div className={`options-list ${isActive ? "active" : ""}`}>
        {!fileUploaded && (
          <div className="option d-flex flex-column gap-3">
            <label
              htmlFor="file-upload"
              className="upload-cell-map upload-content d-flex flex-row align-items-center justify-content-between cursor-pointer mt-3"
            >
              <div className="d-flex flex-column">
                <span className="file-upload-label fs-6 fw-500 main-text-color">
                  {" "}
                  Upload cell map file{" "}
                </span>
                <span className="fs-0-875 fw-300 secondary-text-color">
                  {" "}
                  Only {acceptedFormats} allowed.{" "}
                </span>
                <input
                  type="file"
                  id="file-upload"
                  className="file-upload-input"
                  onChange={handleFileChange}
                  accept={acceptedFormats}
                />
              </div>
              <div className="upload-icon">
                <UploadIcon strokeColor="#8C41EC" />
              </div>
            </label>
            {error && (
              <div className="d-flex flex-column gap-3">
                <WarningAlert show={!!error} message={error} onClose={() => setError("")} />
                {!error.includes("Unable to connect to the server") && (
                  <div className="example-json-container d-flex flex-column gap-2 cursor-auto">
                    <span className="fs-0-875 fw-500 secondary-text-color">
                      Valid example
                    </span>
                    <pre
                      className="example-json"
                    >
                      {`{
  "0": { "x": 0, "y": 0 },    // Cell positions can be any number
  "1": { "x": 1, "y": 0 },    // Each cell must have unique coordinates
  "2": { "x": 2, "y": 0 },    // The grid will adapt to your data
  // ... Add as many cells as needed
  "n": { "x": 19, "y": 11 }   // Last cell can be at any position
}`}
                    </pre>
                    <span className="fs-0-875 fw-300 secondary-text-color mt-2">
                      The grid size will be automatically calculated based on your cell map data
                    </span>
                  </div>
                )}
              </div>
            )}
          </div>
        )}
        {isActive && (
          <UploadedCellmapList
            uploadedFileName={uploadedFileName}
            onFileDelete={handleFileDelete}
          />
        )}
      </div>
    </div>
  );
}

UploadCellmapComponent.propTypes = {
  acceptedFormats: PropTypes.string.isRequired,
  onFileUpload: PropTypes.func,
  onDimensionsChange: PropTypes.func,
  onFileDelete: PropTypes.func,
  setNetworkError: PropTypes.func.isRequired,
};
