import {
  useEffect,
  useState,
  useContext,
  createContext,
  useRef,
  useMemo,
} from "react";
import PropTypes from "prop-types";
import DEFAULT_OPTIONS from "../utils/websocketConfig";

class WebSocketService {
  constructor(url, onMessage, onOpen, onClose, onError) {
    this.url = url || `ws://${DEFAULT_OPTIONS.host}:${DEFAULT_OPTIONS.port}`;
    this.ws = null;
    this.reconnectAttempts = 0;
    this.maxReconnectAttempts = 10;
    this.reconnectDelay = 2000;
    this._subscribers = [];
    this._callbacks = {
      onMessage: onMessage ?? (() => { }),
      onOpen: onOpen ?? (() => { }),
      onClose: onClose ?? (() => { }),
      onError: onError ?? (() => { }),
    };
  }

  connect() {
    console.log(`Attempting to connect to ${this.url}`);
    this.ws = new WebSocket(this.url);

    this.ws.onopen = () => {
      console.log("WebSocket connection opened");
      this.reconnectAttempts = 0;
      this._callbacks.onOpen();
    };

    this.ws.onmessage = (event) => {
      const message = JSON.parse(event.data);
      this._callbacks.onMessage(message);
    };

    this.ws.onclose = (event) => {
      console.log("WebSocket connection closed", event);
      this._callbacks.onClose();
    };

    this.ws.onerror = (error) => {
      const errorMessage = "TTV is not ready to start streaming or a simulation is already in progress";
      console.error("WebSocket error:", error);
      this._callbacks.onError(errorMessage);
      this._attemptReconnect();
    };
  }

  _attemptReconnect() {
    if (this.reconnectAttempts < this.maxReconnectAttempts) {
      this.reconnectAttempts++;
      const delay =
        this.reconnectDelay * this.reconnectAttempts * (Math.random() + 1);
      setTimeout(() => {
        this.connect();
      }, delay);
      return;
    }
    console.error("Max reconnect attempts reached");
  }

  send(message) {
    if (this.ws && this.ws.readyState === WebSocket.OPEN) {
      this.ws.send(JSON.stringify(message));
    } else {
      console.error("WebSocket is not open. Ready state: ", this.ws.readyState);
    }
  }

  close() {
    if (this.ws) {
      this.ws.close();
    }
    this._callbacks.onClose();
  }
}

export const TTVStatuses = {
  Connected: "Connected",
  Disconnected: "Disconnected",
  Errored: "Error",
  SimulationStarted: "SimulationStarted",
  SimulationStopped: "SimulationStopped",
  StreamingStarted: "StreamingStarted",
  StreamingStopped: "StreamingStopped",
};

const TTVStatuesValues = Object.keys(TTVStatuses).map((k) => TTVStatuses[k]);

const TTVCommandNames = {
  StartSimulation: "start_simulation",
  StopSimulation: "stop_simulation",
  StartStreaming: "power_on",
  StopStreaming: "power_off",
  continueSimulation: "continue_simulation",
};

const TTVStatusReceivedMap = {
  simulation_stopped: "SimulationStopped",
  simulation_started: "SimulationStarted",
  simulation_already_running: "SimulationStarted",
  power_on: "StreamingStarted",
  power_off: "StreamingStopped",
};

const checkStatus = (status, ...expectedStatuses) => {
  if (typeof status !== "string") throw "Bad status";
  if (!Array.isArray(expectedStatuses)) throw "Bad status";
  if (TTVStatuesValues.indexOf(status) < 0)
    throw `The status '${status}' is not supported`;

  return expectedStatuses.reduce((prev, expectedStatus) => {
    if (TTVStatuesValues.indexOf(expectedStatus) < 0)
      throw `The expected status '${status}' is not supported`;
    return (
      prev || status.toLocaleLowerCase() === expectedStatus.toLocaleLowerCase()
    );
  }, false);
};

const TTVWSContext = createContext();
const initialState = {
  error: null,
  status: TTVStatuses.Disconnected,
  isConnected: false,
  isSimulationStarted: false,
  isStreamingStarted: false,
  host: null,
  heatmapData: null,
  showErrorDialog: false,
  recordFileUrl: null,
  timeStamp: null,
  plots: [
    {
      id: 0,
      title: "Plot 1",
      isEditing: false,
      canEdit: true,
      selectedCells: [],
      startTime: null,
      data: null,
    },
    {
      id: 1,
      title: "Plot 2",
      isEditing: false,
      canEdit: true,
      selectedCells: [],
      startTime: null,
      data: null,
    },
    /* {
      id: 2,
      title: "Plot 3",
      isEditing: false,
      canEdit: true,
      selectedCells: [],
      startTime: null,
      data: null
    }*/
  ],
};

const mapData = (data) => {
  if (typeof data == "undefined" || data == null) return null;

  console.log('Raw data received in mapData:', data);

  const power_map_data = data?.power_map_data ?? {};
  console.log('Power map data extracted:', power_map_data);

  const powerMap = {
    name: power_map_data.name ?? "",
    heatFlux: {
      average: power_map_data["average_heat_flux"] ?? null,
      peak: power_map_data["peak_heat_flux"] ?? null,
    },
    cols: power_map_data?.cols ?? null,
    rows: power_map_data?.rows ?? null,
    minVal: power_map_data?.min_val ?? null,
    maxVal: power_map_data?.max_val ?? null,
    surfaceArea: power_map_data?.surface_area ?? null,
    unitCellDim: power_map_data?.unit_cell_dim ?? [],
    series: power_map_data?.series ?? [],
  };

  console.log('Processed power map:', powerMap);

  return {
    powerState: !!data["power-state"],
    timeStamp: Number.parseFloat(
      (data["timestamp"] ?? "0").replace(/0+s$/gim, "")
    ),
    powerMap,
  };
};

function getColorForTemperature(temperature, minTemp, maxTemp) {
  if (minTemp >= maxTemp) {
    throw new Error("minTemp should be less than maxTemp");
  }
  const clampedTemp = Math.max(minTemp, Math.min(maxTemp, temperature));
  const ratio = (clampedTemp - minTemp) / (maxTemp - minTemp);
  const red = Math.min(255, Math.max(0, Math.floor(255 * ratio)));
  const blue = 255 - red;

  return `rgb(${red}, 0, ${blue})`;
}

const convertCoordinateToString = (coordinate) => {
  const letters = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";
  return `${letters[coordinate.y]}${coordinate.x + 1}`;
};

const generatePlotData = (
  { selectedCells, startTime, data: rawData },
  heatmapData
) => {
  if (!startTime || !heatmapData) return null; // Skip if start time is not set or heatmap is not defined

  const elapsedTimeInSeconds = Math.floor(
    (performance.now() - startTime) / 1000
  );

  const data = { ...(rawData ?? { labels: [], datasets: [] }) };

  return {
    labels: [...data.labels, elapsedTimeInSeconds],
    datasets: selectedCells.map((cell) => {
      const cellData = heatmapData.find((d) => {
        const cellCoordinate = convertCoordinateToString(d.coordinate);

        return cellCoordinate === cell;
      });
      const temperature = cellData ? cellData.temperature : null;
      const existingDataset = data.datasets.find((d) => d.label === cell);

      return {
        label: cell,
        data: existingDataset
          ? [...existingDataset.data, temperature]
          : [temperature],
        borderColor:
          temperature !== null
            ? getColorForTemperature(temperature, 15, 30)
            : "#000000",
        fill: false,
      };
    }),
  };
};

const updatePlotData = (plot, heatmapData) => {
  return {
    ...plot,
    data: generatePlotData(plot, heatmapData),
  };
};

export const TTVWSProvider = ({ children, url }) => {
  const [socketInstance, setSocketInstance] = useState(null);
  const [state, setState] = useState(initialState);
  const stateRef = useRef(initialState);

  const onMessage = ({ status, data: rawData, download_file }) => {
    const state = stateRef.current;
    console.log('WebSocket message received:', { status, rawData, download_file });
    
    if (
      state.isConnected &&
      status &&
      Object.keys(TTVStatusReceivedMap).indexOf(status) > -1 &&
      TTVStatusReceivedMap[status] !== state.status
    ) {
      console.log('Setting new status:', TTVStatusReceivedMap[status]);
      setStatus(TTVStatusReceivedMap[status]);
      if (
        download_file &&
        (state.recordFileUrl ?? "").indexOf(download_file) == -1
      ) {
        setState((state) => ({
          ...state,
          recordFileUrl: `http://${DEFAULT_OPTIONS.host}:${DEFAULT_OPTIONS.port}${download_file}`,
        }));
      } else if (!download_file && (state.recordFileUrl ?? "").length) {
        setState((state) => ({ ...state, recordFileUrl: null }));
      }
    }

    const data = mapData(rawData);
    console.log('Mapped data:', data);
    
    if (data && data.powerMap?.series) {
      if (status === "power_on") {
        console.log('Setting data due to power_on status');
        setData(data);
      } else if (
        checkStatus(
          state.status,
          TTVStatuses.SimulationStopped,
          TTVStatuses.StreamingStopped,
          TTVStatuses.Disconnected,
          TTVStatuses.Errored
        ) &&
        !data.powerState &&
        state.heatmapData
      ) {
        console.log('Clearing heatmap data due to power state off');
        setState((state) => ({
          ...state,
          heatmapData: null,
          timeStamp: null,
        }));
      }
    }
  };

  const setStatus = (status, message) => {
    if (status == null || TTVStatuesValues.indexOf(status) < 0) {
      throw `The status '${status}' is not supported`;
    }
    setState((state) => ({
      ...state,
      status,
      isConnected: checkStatus(
        status,
        TTVStatuses.Connected,
        TTVStatuses.SimulationStarted,
        TTVStatuses.SimulationStopped,
        TTVStatuses.StreamingStarted,
        TTVStatuses.StreamingStopped
      ),
      isSimulationStarted: checkStatus(status, TTVStatuses.SimulationStarted),
      isStreamingStarted: checkStatus(status, TTVStatuses.StreamingStarted),
      message: message ? message : null,
      host: socketInstance?.url?.replace(/wss?:\/\//gim, "") ?? null,
      showErrorDialog: checkStatus(
        status,
        TTVStatuses.Disconnected,
        TTVStatuses.Errored
      ),
    }));
  };

  const setData = (data) => {
    if (data.powerMap?.series) {
      console.log('Processing series data:', data.powerMap.series);
      const heatmapData = [];
      
      // Imprimir la estructura completa de la primera fila para debug
      console.log('First row structure:', JSON.stringify(data.powerMap.series[0]));
      
      data.powerMap.series.forEach((row, y) => {
        // Extraer los datos de temperatura
        const rowData = row.data || row;
        console.log(`Processing row ${y} with values:`, rowData);
        
        if (Array.isArray(rowData)) {
          rowData.forEach((temp, x) => {
            heatmapData.push({
              coordinate: { x, y },
              temperature: temp
            });
          });
        }
      });

      console.log('Generated heatmap data:', {
        totalCells: heatmapData.length,
        cellsWithTemp: heatmapData.filter(d => d.temperature !== null).length,
        sampleTemps: heatmapData.slice(0, 3).map(d => d.temperature),
        dimensions: { rows: data.powerMap.rows, cols: data.powerMap.cols }
      });

      setState((state) => ({
        ...state,
        heatmapData,
        plots: state.plots.map((p) => updatePlotData(p, heatmapData)),
        timeStamp: data.timeStamp,
      }));
    }
  };

  // REACT Bug with useState & onMessage WS
  // https://github.com/facebook/react/issues/30454
  useEffect(() => {
    stateRef.current = state;
  }, [state]);

  useEffect(() => {
    const instance = new WebSocketService(
      url ?? "",
      (data) => data && onMessage(data),
      () => setStatus(TTVStatuses.Connected),
      () => setStatus(TTVStatuses.Disconnected),
      (error) => setStatus(TTVStatuses.Errored, error)
    );
    instance.connect();
    setSocketInstance(instance);
    return () => {
      instance.close();
      setSocketInstance(null);
    };
  }, [url]);

  const startSimulation = () => {
    const { isConnected, isSimulationStarted, isStreamingStarted } = state;
    if (!isConnected || isSimulationStarted || isStreamingStarted) {
      throw "TTV is not ready to start a simulation or a simulation is already in progress";
    }
    socketInstance.send({
      command: TTVCommandNames.StartSimulation,
    });
  };

  const stopSimulation = () => {
    const { status } = state;
    if (!checkStatus(status, TTVStatuses.SimulationStarted)) {
      throw "TTV simulation is not started";
    }
    socketInstance.send({
      command: TTVCommandNames.StopSimulation,
    });
  };

  const sendPowermap = (name, powerMapRaw) => {
    if (typeof powerMapRaw !== "string") return;
    let maxValue = 0;
    const lines = powerMapRaw.split("\n");
    if (lines?.length < 2) return;

    // Filtrar líneas vacías y excluir la primera fila
    const dataRows = lines
      .slice(1)  // Excluir la primera fila
      .filter(line => line.trim() !== "")  // Eliminar líneas vacías
      .map(line => 
        line.split(",")
          .map(value => {
            const trimmed = value.trim();
            return trimmed === "" ? 0 : Number(trimmed);
          })
          .slice(0, 20)  // Asegurar 20 columnas
      );

    // Calcular el valor máximo
    maxValue = Math.max(...dataRows.flat());

    // Crear la estructura exacta requerida
    const data = {
      powermap: {
        max_val: maxValue,
        series: dataRows,
        dimension: {
          x: 20,  // Dimensión fija
          y: 12   // Dimensión fija
        },
        name
      }
    };

    console.log('Sending powermap with dimensions:', {
      rows: dataRows.length,
      cols: dataRows[0]?.length,
      maxValue
    });

    socketInstance.send(data);
  };

  const startStreaming = () => {
    const { isConnected, isSimulationStarted, isStreamingStarted } = state;
    if (!isConnected || isSimulationStarted || isStreamingStarted) {
      throw "TTV is not ready to start streaming or a simulation is already in progress";
    }

    socketInstance.send({
      command: TTVCommandNames.StartStreaming,
    });
  };

  const stopStreaming = () => {
    const { isStreamingStarted } = state;
    if (!isStreamingStarted) {
      throw "TTV streaming is not started";
    }
    socketInstance.send({
      command: TTVCommandNames.StopStreaming,
    });
  };

  const setPlotEditing = (plotId, value) => {
    setState((state) => ({
      ...state,
      plots: state.plots.map((p) => ({
        ...p,
        isEditing: p.id == plotId && !!value,
      })),
    }));
  };

  const removePlotCell = (plotId, cellToRemove) =>
    setState((state) => {
      const plotIdx = state.plots.findIndex(({ id }) => id == plotId);
      if (plotIdx < 0 || !cellToRemove) return state;

      const plot = state.plots[plotIdx];
      const selectedCells = plot.selectedCells.filter(
        (cell) => cell != cellToRemove
      );

      if (plot.selectedCells.length == selectedCells.length) return state;

      const plots = [...state.plots];
      plots[plotIdx] = {
        ...plot,
        startTime: !selectedCells.length ? null : plot.startTime,
        selectedCells,
      };
      return {
        ...state,
        plots,
      };
    });

  const addPlotCell = (plotId, cellToAdd) =>
    setState((state) => {
      const plotIdx = state.plots.findIndex(({ id }) => id == plotId);
      if (plotIdx < 0 || !cellToAdd) return state;

      const plot = state.plots[plotIdx];
      if (plot.selectedCells.indexOf(cellToAdd) != -1) return state;

      if (!plot.startTime) {
        plot.startTime = performance.now();
      }

      const plots = [...state.plots];

      plots[plotIdx] = {
        ...plot,
        selectedCells: [...plot.selectedCells, cellToAdd].sort(),
      };

      return {
        ...state,
        plots,
      };
    });

  // const [currentPlot] = state.plots.filter((p) => p.isEditing);
  // const allSelectedCells = Array.from(new Set(state.plots?.reduce((retrieve, plot) => [...retrieve, ...(plot?.selectedCells || [])], []).sort()))

  //Using memo to ensure the compute occurs only when plots changes to retreive the current plot and all selected cells.
  const { currentPlot, allSelectedCells } = useMemo(() => {
    const [currentPlot] = state.plots.filter((p) => p.isEditing);
    const allSelectedCells = Array.from(
      new Set(
        state.plots
          ?.reduce(
            (retrieve, plot) => [...retrieve, ...(plot?.selectedCells || [])],
            []
          )
          .sort()
      )
    );
    return {
      currentPlot,
      allSelectedCells,
    };
  }, [state.plots]);

  return (
    <TTVWSContext.Provider
      value={{
        ...state,
        currentPlot: currentPlot ?? null,
        allSelectedCells,
        startSimulation,
        stopSimulation,
        startStreaming,
        stopStreaming,
        sendPowermap,
        setPlotEditing,
        removePlotCell,
        addPlotCell,
      }}
    >
      {children}
    </TTVWSContext.Provider>
  );
};

export const useTTVWSContext = () => {
  const context = useContext(TTVWSContext);
  if (!context) throw "TTWS cannot be used outside TTVWSProvider";
  return context;
};

TTVWSProvider.propTypes = {
  children: PropTypes.any.isRequired,
  url: PropTypes.any,
};
