import { useTTVWSContext } from "../../api/webSocketService";
import CircleIcon from "../../utils/icons/CircleIcon";
import DEFAULT_OPTIONS from "../../utils/websocketConfig";
import PropTypes from "prop-types";
import { CustomTooltip } from "../../utils/dialogCommonProps";

const ICON_STYLE = { width: "0.25rem" };
const STATUS_CONNECTED_COLOR = "#007D4E";
const STATUS_DISCONNECTED_COLOR = "#E3203F";

export default function IPStatus({ drawerOpen }) {
  const { isConnected } = useTTVWSContext();
  const ipAddress = `${DEFAULT_OPTIONS.host}:${process.env.PORT || 3000}`;

  return (
    <div
      className="d-flex gap-2 justify-content-center"
      style={{
        bottom: "0",
        padding: "0.6rem",
        borderBottom: "1px solid #d0d5dd",
        minHeight: "2.5rem",
      }}
    >
      {drawerOpen ? (
        <>
          <span className="fs-6 fw-500">{ipAddress}</span>
          {isConnected ? (
            <div className="status-connected d-flex gap-2">
              <CircleIcon style={ICON_STYLE} color={STATUS_CONNECTED_COLOR} />
              <span className="fs-6 fw-500">Connected</span>
            </div>
          ) : (
            <div className="status-off d-flex gap-2">
              <CircleIcon style={ICON_STYLE} color={STATUS_DISCONNECTED_COLOR} />
              <span className="fs-6 fw-500">Off</span>
            </div>
          )}
        </>
      ) : (
        <CustomTooltip 
          title={
            <div>
              <div>{ipAddress}</div>
              <div>{isConnected ? "Connected" : "Off"}</div>
            </div>
          }
        >
          <div className="d-flex align-items-center gap-1">
            <span className="fs-6 fw-500">IP</span>
            <CircleIcon 
              style={ICON_STYLE} 
              color={isConnected ? STATUS_CONNECTED_COLOR : STATUS_DISCONNECTED_COLOR} 
            />
          </div>
        </CustomTooltip>
      )}
    </div>
  );
}

IPStatus.propTypes = {
  drawerOpen: PropTypes.bool.isRequired,
};
