import { useState, useCallback, useEffect } from "react";
import LeftSidebarComponent from "./Sidebars/LeftSidebar/LeftSidebarComponent";
import RightSidebarComponent from "../components/Sidebars/RightSidebar/RightSidebarComponent";
import HeatmapComponent from "./Heatmap/HeatmapComponent";
import PlotsContainer from "./Plots/PlotsContainer";
import { useTTVWSContext } from "../api/webSocketService";
import loadingGif from "../assets/gifs/loading.gif";
import StaticLoadingIcon from "../utils/icons/StaticLoadingIcon";
import UploadInstructions from "../utils/components/ErrorStates/UploadInstructions";
import CloseIcon from "../utils/icons/CloseIcon";

export default function ExperimentDashboard() {
  const [hasUploaded, setHasUploaded] = useState(false);
  const [loading, setLoading] = useState(false);
  const [heatmapDimensions, setHeatmapDimensions] = useState({
    rows: 0,
    cols: 0,
  });
  const [isHeatmap, setIsHeatmap] = useState(true);
  const [heatmapVisible, setHeatmapVisible] = useState(false);
  const [showDownloadLink, setShowDownloadLink] = useState(true); // State to control visibility

  const {
    heatmapData: wsHeatmapData,
    heatmapDimensions: wsHeatmapDimensions,
    currentPlot,
    addPlotCell,
    removePlotCell,
    recordFileUrl,
  } = useTTVWSContext();

  const showRecordFile = !!recordFileUrl;

  const selectedCells = currentPlot?.selectedCells ?? [];

  useEffect(() => {
    if (wsHeatmapDimensions) {
      setHeatmapDimensions(wsHeatmapDimensions);
    }
  }, [wsHeatmapDimensions]);

  useEffect(() => {
    console.log('wsHeatmapData changed:', wsHeatmapData);
  }, [wsHeatmapData]);

  useEffect(() => {
    console.log('wsHeatmapDimensions changed:', wsHeatmapDimensions);
  }, [wsHeatmapDimensions]);

  useEffect(() => {
    console.log('heatmapDimensions changed:', heatmapDimensions);
  }, [heatmapDimensions]);

  const handleFileUpload = () => {
    console.log('Starting file upload...');
    setLoading(true);
    
    // Immediately set states needed for rendering
    setHasUploaded(true);
    setIsHeatmap(true);
    setHeatmapVisible(true);
    setLoading(false);
    
    console.log('Upload complete. Current state:', {
      hasUploaded: true,
      isHeatmap: true,
      loading: false,
      heatmapVisible: true,
      wsHeatmapData,
      heatmapDimensions
    });
  };

  // Add effect to handle WebSocket data updates
  useEffect(() => {
    if (wsHeatmapData && wsHeatmapData.length > 0) {
      console.log('WebSocket data received:', {
        dataLength: wsHeatmapData.length,
        dimensions: heatmapDimensions
      });
      
      // Ensure the component is visible when data arrives
      setHeatmapVisible(true);
      setHasUploaded(true);
    }
  }, [wsHeatmapData]);

  // Add effect to handle dimension updates
  useEffect(() => {
    if (heatmapDimensions.rows > 0 && heatmapDimensions.cols > 0) {
      console.log('Dimensions updated:', heatmapDimensions);
    }
  }, [heatmapDimensions]);

  const handleDimensionsChange = useCallback((newDimensions) => {
    setHeatmapDimensions(newDimensions);
  }, []);

  const handleCellClick = (coordinate) => {
    console.log("Clicked on coordinate:", coordinate);
  };

  const handleCellSelect = (cell) => {
    if (!currentPlot) return;
    selectedCells.indexOf(cell) === -1
      ? addPlotCell(currentPlot.id, cell)
      : removePlotCell(currentPlot.id, cell);
  };

  const handleCloseDownloadLink = () => {
    setShowDownloadLink(false);
  };

  return (
    <div className="main-content">
      <LeftSidebarComponent
        onFileUpload={handleFileUpload}
        onDimensionsChange={handleDimensionsChange}
        setIsHeatmap={setIsHeatmap}
        isHeatmap={isHeatmap}
      />
      <div className="content d-flex align-items-center justify-content-center position-relative">
        {showDownloadLink && showRecordFile && (
          <div
            style={{
              position: "absolute",
              zIndex: "9999",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              borderRadius: "1rem",
              background: "rgb(252, 252, 253)",
              boxShadow: "rgba(146, 146, 146, 0.12) 0rem 0rem 3.33rem 1.67rem",
              backdropFilter: " blur(0.5rem)",
              padding: "1.5rem",
            }}
          >
            <div>
              <div className="d-flex justify-content-end">
                <CloseIcon onClick={handleCloseDownloadLink} />
              </div>
              <div className="w-100 d-flex justify-content-center">
                <a href={recordFileUrl} className="title" style={{ width: "33rem", color: "blue"}}>
                  Download the record file
                </a>
              </div>
            </div>

          </div>
        )}

        {loading ? (
          <div className="loading-gif d-flex flex-column align-items-center">
            <div style={{ height: "6rem" }}>
              <img
                src={loadingGif}
                alt="Loading..."
                style={{ width: "4rem", height: "4rem" }}
              />
            </div>
            <UploadInstructions />
          </div>
        ) : hasUploaded ? (
          isHeatmap ? (
            <div
              className={`scrollable-container fade-in ${heatmapVisible ? "visible" : ""}`}
            >
              <div
                className="display-content d-flex justify-content-center gap-3 position-relative"
                style={{
                  height: "calc(100vh + -4rem)",
                  padding: "0.5rem 0",
                  alignItems: "center",
                }}
              >
                <HeatmapComponent
                  data={wsHeatmapData || []}
                  onCellClick={handleCellClick}
                  onCellSelect={handleCellSelect}
                  selectedCells={selectedCells}
                  rows={heatmapDimensions.cols || 20}
                  cols={heatmapDimensions.rows || 12}
                  enable={!!currentPlot}
                  plotIndex={currentPlot?.id ?? 0}
                />
              </div>
              {!navigator.onLine && (
                <div className="offline-warning">
                  You are currently offline
                </div>
              )}
            </div>
          ) : (
            <PlotsContainer />
          )
        ) : (
          <div className="upload-instructions d-flex flex-column align-items-center">
            <div style={{ height: "6rem" }}>
              <StaticLoadingIcon />
            </div>
            <UploadInstructions />
          </div>
        )}
      </div>
      {hasUploaded && heatmapDimensions && (
        <RightSidebarComponent heatmapDimensions={heatmapDimensions} />
      )}
    </div>
  );
}