import PropTypes from "prop-types";
import chroma from "chroma-js";
import { temperatureColorGradient } from "../../utils/temperatureColorGradient";

export default function GradientScale({
  minTemp,
  maxTemp,
  values,
  colorScale,
  numStops = 5,
  orientation = "vertical",
  labelFormatter = (value) => `${value.toFixed(1)}°C`,
}) {
  const isTemperatureMode = minTemp !== undefined && maxTemp !== undefined;
  const minValue = isTemperatureMode ? minTemp : Math.min(...values);
  const maxValue = isTemperatureMode ? maxTemp : Math.max(...values);

  const gradientColors = isTemperatureMode
    ? chroma
        .scale(temperatureColorGradient)
        .domain([minTemp, maxTemp])
        .colors(100)
    : Array.from({ length: numStops }, (_, i) => {
        const ratio = i / (numStops - 1);
        return colorScale(minValue + (maxValue - minValue) * ratio).hex();
      });

  const gradientStyle = {
    background: `linear-gradient(to ${orientation === "vertical" ? "top" : "right"}, ${gradientColors.join(", ")})`,
    height: orientation === "vertical" ? "100%" : "20px",
    width: orientation === "vertical" ? "1rem" : "100%",
    position: "relative",
    borderRadius: "0.125rem",
    margin: orientation === "vertical" ? "0" : "10px 0px 4px",
  };

  const labelStyle = {
    position: "absolute",
    left: orientation === "vertical" ? "35px" : "0",
    fontSize: "0.9rem",
    fontWeight: "400",
    color: "#344054",
  };

  const labels = Array.from({ length: numStops }, (_, i) => {
    const ratio = i / (numStops - 1);
    return minValue + (maxValue - minValue) * ratio;
  });

  return (
    <div
      style={{
        display: "flex",
        alignItems: "center",
        position: "relative",
        flexDirection: orientation === "vertical" ? "row" : "column",
      }}
    >
      <div style={gradientStyle} />
      <div
        style={{
          height: orientation === "vertical" ? "94%" : "auto",
          width: orientation === "vertical" ? "auto" : "100%",
          position: "relative",
          right: orientation === "vertical" ? "1.5rem" : "0",
          display: "flex",
          justifyContent: "space-between",
        }}
      >
        {labels.map((label, index) => (
          <span
            key={index}
            style={{
              ...labelStyle,
              top:
                orientation === "vertical"
                  ? `${(1 - index / (numStops - 1)) * 100}%`
                  : "auto",
              transform:
                orientation === "vertical" && index === labels.length - 1
                  ? "translateY(-100%)"
                  : "translateY(0)",
              margin: orientation === "vertical" ? "0" : "0 5px",
            }}
          >
            {labelFormatter(label)}
          </span>
        ))}
      </div>
    </div>
  );
}

GradientScale.propTypes = {
  minTemp: PropTypes.number,
  maxTemp: PropTypes.number,
  values: PropTypes.arrayOf(PropTypes.number),
  colorScale: PropTypes.func,
  numStops: PropTypes.number,
  orientation: PropTypes.oneOf(["vertical", "horizontal"]),
  labelFormatter: PropTypes.func,
};
