import { useState, useEffect, useRef } from "react";
import { useTTVWSContext } from "../../../api/webSocketService";
import DropDownOpenIcon from "../../../utils/icons/DropDownOpenIcon";
import DropDownCloseIcon from "../../../utils/icons/DropDownCloseIcon";
import MaxJuncTempIcon from "../../../utils/icons/MaxJuncTempIcon";
import PowerIcon from "../../../utils/icons/PowerIcon";
import ArrowDownIcon from "../../../utils/icons/ArrowDownIcon";
import ArrowUpIcon from "../../../utils/icons/ArrowUpIcon";
import QuestionPopOver from "../../../utils/components/PopOver/QuestionPopOver";

export default function PowermapInfo() {
  const [isActive, setIsActive] = useState(false);
  const [maxTemperature, setMaxTemperature] = useState(null);
  const [totalPower, setTotalPower] = useState(null);
  const [maxTempDirection, setMaxTempDirection] = useState(null);
  const prevMaxTemp = useRef(null);
  const { heatmapData, powerMap, isSimulationStarted, isStreamingStarted } =
    useTTVWSContext();

  const toggleDropdown = () => {
    setIsActive(!isActive);
  };

  useEffect(() => {
    if (heatmapData?.length > 0) {
      const temperatures = heatmapData.map((item) => item.temperature);
      const maxTemp = Math.max(...temperatures);

      if (prevMaxTemp.current !== null) {
        setMaxTempDirection(maxTemp > prevMaxTemp.current ? "up" : "down");
      }

      prevMaxTemp.current = maxTemp;
      setMaxTemperature(maxTemp);
    }
    if (powerMap) {
      const totalPower = powerMap.series.reduce((acc, series) => {
        return acc + series.data.reduce((sum, value) => sum + value, 0);
      }, 0);
      setTotalPower(totalPower);
    }
  }, [heatmapData, powerMap]);

  // Do not show real-time data if the power is off or if the recording has not started
  if (!isSimulationStarted && !isStreamingStarted) {
    return null;
  }

  return (
    <div className="select-menu d-flex flex-column" data-testid="powermap-info">
      <div
        className="select d-flex align-items-center justify-content-between"
        onClick={toggleDropdown}
      >
        <div className="d-flex align-items-center">
          {isActive ? <DropDownOpenIcon /> : <DropDownCloseIcon />}
          <span
            className={`fs-6 fw-500 ${isActive ? "main-text-color" : "closed-text-color"}`}
          >
            Real-time data
          </span>
        </div>
      </div>
      {isActive && (
        <div className="options-list active m-0">
          <div className="option d-flex flex-column gap-4">
            <div className="d-flex flex-column">
              {maxTemperature !== null && (
                <div className="d-flex flex-row w-100 align-items-center gap-3 mt-3">
                  <MaxJuncTempIcon />
                  <div className="d-flex flex-column">
                    <span className="fs-6 fw-400 secondary-text-color">
                      Max junction temp.
                    </span>
                    <div className="d-flex align-items-center">
                      {maxTempDirection &&
                        (maxTempDirection === "up" ? (
                          <div className="d-flex align-items-center gap-2">
                            <span
                              className="fs-6 fw-400"
                              style={{ color: "#E3203F" }}
                            >
                              {maxTemperature.toFixed(2)}°C
                            </span>
                            <ArrowUpIcon />
                          </div>
                        ) : (
                          <div className="d-flex align-items-center gap-2">
                            <span
                              className="fs-6 fw-400"
                              style={{ color: "#8C41EC" }}
                            >
                              {maxTemperature.toFixed(2)}°C
                            </span>
                            <ArrowDownIcon />
                          </div>
                        ))}
                      <QuestionPopOver
                        style={{ width: "1em", height: "1rem" }}
                        text="The highest temperature experienced at the hottest point within the TTV."
                      />
                    </div>
                  </div>
                </div>
              )}
            </div>
            <div className="d-flex flex-column d-none">
              {totalPower !== null && (
                <div
                  className="d-flex flex-row w-100 align-items-center gap-3"
                  style={{ padding: "0 1rem" }}
                >
                  <PowerIcon />
                  <div className="d-flex flex-column">
                    <span className="fs-6 fw-400 secondary-text-color">
                      Total power
                    </span>
                    <span className="fs-6 fw-400 secondary-text-color">
                      {totalPower.toFixed(2)} W
                    </span>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
