import { useState, useEffect, useCallback } from "react";
import PropTypes from "prop-types";
import GradientScale from "./GradientScale";
import CheckIcon from "../../utils/icons/CheckIcon";
import chroma from "chroma-js";
import './Heatmap.css';  // Importamos los estilos

// Definimos una escala de colores que coincida con la segunda imagen
const temperatureColorScale = [
  '#38216B',  // Morado oscuro para temperaturas más bajas (~38°C)
  '#8B4B8B',  // Morado medio
  '#D85D3D',  // Naranja rojizo
  '#F5B642',  // Naranja claro
  '#FFED38'   // Amarillo para temperaturas más altas (~55°C)
];

export default function HeatmapComponent({
  data,
  onCellClick,
  onCellSelect,
  selectedCells,
  enable,
  rows,
  cols,
  plotIndex,
}) {
  const [heatmapData, setHeatmapData] = useState({});
  const [minTemp, setMinTemp] = useState(null);
  const [maxTemp, setMaxTemp] = useState(null);
  const isEnabled = !!enable;

  useEffect(() => {
    console.log('Rendering heatmap with:', { 
      data, 
      rows, 
      cols,
      dataLength: data?.length,
      activeCells: data?.filter(d => d.coordinate)?.length,
      cellsWithTemp: data?.filter(d => d.coordinate && d.temperature !== null)?.length,
      firstCell: data?.[0],
      coordinates: data?.map(d => d.coordinate)
    });
    
    if (Array.isArray(data)) {
      const newHeatmapData = {};
      let tempMin = Infinity;
      let tempMax = -Infinity;
      
      data.forEach(({ coordinate, temperature }) => {
        if (coordinate) {
          const row = String.fromCharCode(65 + coordinate.y);
          const col = coordinate.x + 1;
          const key = `${row}${col}`;
          
          newHeatmapData[key] = temperature !== null ? temperature : 0;
          
          if (temperature !== null) {
            tempMin = Math.min(tempMin, temperature);
            tempMax = Math.max(tempMax, temperature);
          }
        }
      });
      
      console.log('Temperature range:', { tempMin, tempMax });
      setHeatmapData(newHeatmapData);
      
      if (tempMin !== Infinity && tempMax !== -Infinity) {
        setMinTemp(tempMin);
        setMaxTemp(tempMax);
      }
    }
  }, [data, rows, cols]);

  const getColorForTemperature = (temp) => {
    if (temp === null) return "#e6e6e6";
    
    // Definimos el rango de temperaturas observado
    const minValidTemp = 38.0;  // Temperatura mínima observada
    const maxValidTemp = 55.0;  // Temperatura máxima observada
    
    // Aseguramos que la temperatura está dentro del rango
    const clampedTemp = Math.max(minValidTemp, Math.min(maxValidTemp, temp));
    
    // Calculamos la posición normalizada en la escala de colores
    const normalizedTemp = (clampedTemp - minValidTemp) / (maxValidTemp - minValidTemp);
    
    // Creamos la escala de colores y la aplicamos
    const colorScale = chroma.scale(temperatureColorScale).mode('lab');
    return colorScale(normalizedTemp).hex();
  };

  const handleCellClick = useCallback(
    (coordinate) => {
      onCellClick(coordinate);
      onCellSelect(coordinate);
    },
    [onCellClick, onCellSelect]
  );

  const selectedBackgroundColors = ["#0097A7", "#F05D0E"];
  const selectedBackgroundColor =
    selectedBackgroundColors[plotIndex % selectedBackgroundColors.length];

  return (
    <div style={{ display: "flex", position: "relative", gap: "0.6rem" }}>
      <div className="cellmap-container" style={{ 
        overflow: "auto", 
        flex: 1,
        padding: "1.5rem",
        backgroundColor: "#fff",
        borderRadius: "0.5rem",
        boxShadow: "0 0 10px rgba(0,0,0,0.05)"
      }}>
        <div 
          className="heatmap"
          style={{
            backgroundColor: "#fff",
            padding: "1rem",
            width: "fit-content",
            minWidth: "100%"
          }}
        >
          {/* Números de columna */}
          <div
            style={{
              display: "flex",
              marginLeft: "3rem",
              marginBottom: "0.75rem",
              gap: "2px"
            }}
          >
            {Array.from({ length: rows }).map((_, colIndex) => (
              <div
                key={colIndex}
                style={{
                  width: "2.5rem",
                  textAlign: "center",
                  fontSize: "0.875rem",
                  color: "#666"
                }}
              >
                {colIndex + 1}
              </div>
            ))}
          </div>

          {/* Grid con letras laterales */}
          <div style={{ display: "flex", flexDirection: "column", gap: "2px" }}>
            {Array.from({ length: cols }).map((_, rowIndex) => {
              const row = String.fromCharCode(65 + rowIndex);
              return (
                <div key={row} style={{ 
                  display: "flex",
                  alignItems: "center",
                  gap: "1rem"
                }}>
                  {/* Letra de fila */}
                  <div
                    style={{
                      width: "2rem",
                      textAlign: "center",
                      fontSize: "0.875rem",
                      color: "#666",
                    }}
                  >
                    {row}
                  </div>
                  
                  {/* Celdas de la fila */}
                  <div style={{ display: "flex", gap: "2px" }}>
                    {Array.from({ length: rows }).map((_, colIndex) => {
                      const col = colIndex + 1;
                      const coordinate = `${row}${col}`;
                      const temperature = heatmapData[coordinate];
                      const color = getColorForTemperature(temperature);
                      const isSelected = selectedCells.includes(coordinate);

                      return (
                        <div
                          key={coordinate}
                          className={`heatmap-cell ${isSelected ? "selected" : ""}`}
                          style={{
                            width: "2.5rem",
                            height: "2.5rem",
                            backgroundColor: color,
                            border: "1px solid rgba(224, 224, 224, 0.5)",
                            cursor: isEnabled ? "pointer" : "default",
                            transition: "all 0.2s ease",
                            position: "relative",
                            display: "flex",
                            alignItems: "center",
                            justifyContent: "center"
                          }}
                          onClick={isEnabled ? () => handleCellClick(coordinate) : null}
                          disabled={!isEnabled}
                        >
                          {temperature != null && (
                            <span 
                              className="temperature-text"
                              style={{ 
                                fontSize: "0.7rem", 
                                color: chroma(color).luminance() > 0.5 ? "#000" : "#fff",
                                position: "absolute",
                                zIndex: 1,
                                opacity: 0,
                                transition: "opacity 0.2s ease",
                                pointerEvents: "none"
                              }}
                            >
                              {temperature.toFixed(1)}°C
                            </span>
                          )}
                          {isSelected && (
                            <div
                              className="heatmap-cell-selected"
                              style={{ 
                                backgroundColor: selectedBackgroundColor,
                                position: "absolute",
                                inset: 0,
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center",
                                opacity: 0.7
                              }}
                            >
                              <CheckIcon style={{ color: "white", fontSize: "1rem" }} />
                            </div>
                          )}
                        </div>
                      );
                    })}
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
      {minTemp !== null && maxTemp !== null && minTemp !== maxTemp && (
        <GradientScale
          minTemp={minTemp}
          maxTemp={maxTemp}
          colorScale={getColorForTemperature}
          numStops={5}
          orientation="vertical"
        />
      )}
    </div>
  );
}

HeatmapComponent.propTypes = {
  data: PropTypes.arrayOf(
    PropTypes.shape({
      coordinate: PropTypes.shape({
        x: PropTypes.number,
        y: PropTypes.number,
      }),
      temperature: PropTypes.number,
    })
  ).isRequired,
  enable: PropTypes.bool,
  onCellClick: PropTypes.func.isRequired,
  onCellSelect: PropTypes.func.isRequired,
  selectedCells: PropTypes.arrayOf(PropTypes.string).isRequired,
  rows: PropTypes.number.isRequired,
  cols: PropTypes.number.isRequired,
  plotIndex: PropTypes.number.isRequired,
};
