import { useState } from "react";
import DropDownCloseIcon from "../../../utils/icons/DropDownCloseIcon";
import DropDownOpenIcon from "../../../utils/icons/DropDownOpenIcon";
import PlotSelectContainer from "../../Plots/PlotSelectContainer";
import { useTTVWSContext } from "../../../api/webSocketService";
import PropTypes from "prop-types";

export default function RTDSettings({ onSave, onAdd }) {
  const [isActive, setIsActive] = useState(false);
  const { plots, setPlotEditing, removePlotCell } = useTTVWSContext();

  const toggleDropdown = () => {
    setIsActive(!isActive);
  };

  const mapPlotComponent = ({ id, title, selectedCells, isEditing }, index) => {
    const circleIconColors = ["#0097A7", "#F05D0E"];
    const circleIconColor = circleIconColors[index % circleIconColors.length];

    return (
      <PlotSelectContainer
        key={`plot_${id}_${index}`}
        title={title}
        isEditing={isEditing}
        selectedCells={selectedCells}
        onAdd={() => {
          setPlotEditing(id, true);
          onAdd && onAdd();
        }}
        recordFileUrl
        onSave={() => {
          setPlotEditing(id, false);
          onSave && onSave();
        }}
        onRemovedCell={(cell) => removePlotCell(id, cell)}
        circleIconColor={circleIconColor}
        plotIndex={index}
      />
    );
  };

  return (
    <div className="rtd-settings w-100 gap-3">
      <div
        className="select-menu d-flex flex-column w-100 gap-4"
        data-testid="file-upload"
      >
        <div
          className="select d-flex align-items-center justify-content-between"
          onClick={toggleDropdown}
        >
          <div className="d-flex align-items-center">
            {isActive ? <DropDownOpenIcon /> : <DropDownCloseIcon />}
            <span
              className={`fs-6 fw-500 ${isActive ? "main-text-color" : "closed-text-color"}`}
            >
              RTD settings
            </span>
          </div>
        </div>
      </div>
      <div className="scrollable-content">
        <div className="d-flex flex-column gap-4">
          {isActive && <>{plots?.map(mapPlotComponent)}</>}
        </div>
      </div>
    </div>

  );
}

RTDSettings.propTypes = {
  onSave: PropTypes.func,
  onAdd: PropTypes.func,
};
