import PropTypes from "prop-types";

const formatHeatFlux = (value, scale = 10000) => {
  return (value / scale).toFixed(2);
};

export default function PowermapGradientScale({
  values,
  colorScale,
  numStops = 4,
}) {
  const minValue = Math.min(...values);
  const maxValue = Math.max(...values);

  const gradientStops = Array.from({ length: numStops }, (_, i) => {
    const ratio = i / (numStops - 1);
    return colorScale(minValue + (maxValue - minValue) * ratio).hex();
  });

  const gradientStyle = {
    background: `linear-gradient(to right, ${gradientStops.join(", ")})`,
    height: "20px",
    width: "100%",
    margin: "10px 0px 4px",
  };

  const labelValues = Array.from({ length: numStops }, (_, i) => {
    const ratio = i / (numStops - 1);
    return minValue + (maxValue - minValue) * ratio;
  });

  return (
    <div
      className="d-flex flex-column position-absolute"
      style={{ width: "35%", bottom: "1rem", left: "1rem" }}
    >
      <span className="fs-0-875 grey-text-color">Heat Flux [W/m²]</span>
      <div style={{ ...gradientStyle, borderRadius: "0.125rem" }} />
      <div className="d-flex justify-content-between">
        {labelValues.map((value, index) => (
          <span
            key={index}
            className="fw-400 fs-0-875 secondary-text-color"
            style={{ margin: "0 5px" }}
          >
            {formatHeatFlux(value)}
          </span>
        ))}
      </div>
    </div>
  );
}

PowermapGradientScale.propTypes = {
  values: PropTypes.arrayOf(PropTypes.number).isRequired,
  colorScale: PropTypes.func.isRequired,
  numStops: PropTypes.number,
};
